import React from "react";
import {Link, Redirect} from "react-router-dom";


class EditProfile extends React.Component{
    constructor(props){
        super()
        this.state=props.details;
        this.state.redirectToProfile=false;
        this.state.emailChanWarningDiaplayed=false;
        this.state.otherErr="";
        this.state.badErr="";


        this.setBadErr=this.setBadErr.bind(this);
        this.setOtherErr=this.setOtherErr.bind(this);
        this.redirectToProfile=this.redirectToProfile.bind(this);
        this.emailChanWarnDiaplUpdt=this.emailChanWarnDiaplUpdt.bind(this);
    }

    componentDidMount(){
        const {username, email, profile_pix, emailVerified, login}=this.state;
        if((!username || !email || !profile_pix || emailVerified===null) && login){
            this.setState({redirectToProfile:true});
        }

        let currentPageName=window.location.pathname;
        this.props.updateFunct.updatePageName(currentPageName);
    }

    static getDerivedStateFromProps(prevProps, state){
        if(state.login!==prevProps.details.login){
            let newLogin=prevProps.details.login;
            return({login:newLogin});
        }
        return({});
    }


    redirectToProfile(){
        this.setState({redirectToProfile:true});
    }

    emailChanWarnDiaplUpdt(){
        this.setState({emailChanWarningDiaplayed:true});
    }

    setBadErr(badErr){
        this.setState({badErr:badErr});
    }

    setOtherErr(otherErr){
        this.setState({otherErr:otherErr});
    }

    render(){
        const {
            login,
            logout,
            firstName,
            lastName,
            email,
            gender,
            profile_pix,       
            emailChanWarningDiaplayed,
            otherErr,
            badErr,
            fetchURL,
            redirectToProfile
        } = this.state;

        let linkHash=window.location.hash;
    
        let EPFunction={
            fetchApi:this.fetchApi,
            setBadErr:this.setBadErr,
            setOtherErr:this.setOtherErr,
            redirectToProfile:this.redirectToProfile,
            deletAllState:this.props.updateFunct.deletAllState,
            fetchURL:fetchURL,
            login:login,
            profile_pix:profile_pix,
            redirectToProfileStatus:redirectToProfile
        };

        let DPPixDetails={
            gender:gender,
            profile_pix:profile_pix,
            redirectToProfile:this.state.redirectToProfile
        };

        return(
            <React.Fragment>
                {/* when not loged in */}
                {login ? null : <Redirect to="/"/>}
                {
                    login ?
                    null : logout ? null : sessionStorage.setItem("loginFirst", true)
                }
                {
                    login ?
                    null : logout ? null : sessionStorage.setItem("loginRedirect", this.state.currentPageName)
                }
                {
                    login ?
                    null : logout ? null : <Redirect to="login"/>
                }
                {/* {otherErr ? <p className='errorMeg'>{otherErr}</p> : null} */}
                {otherErr.length ? <p className='errorMeg'>Fatal Error, Please contact the admin</p> : null}
                {badErr ? <p className='errorMeg'>{badErr}</p> : null}

                {!linkHash ? <Redirect to="profile"/> : null}
                {redirectToProfile ? <Redirect to="profile"/> : null}
                <h3>Edit ptofile</h3>                

                {linkHash==="#1" ? <EditFirstName firstName={firstName} EPFunction={EPFunction}/> : null}
                
                {linkHash==="#2" ? <EditLastName lastName={lastName} EPFunction={EPFunction}/> : null}
                
                {linkHash==="#7" ? <AccDelWarning profile_pix={profile_pix} EPFunction={EPFunction}/> : null}

                {linkHash==="#8" ? <DeleteProfilePix DPPixDetails={DPPixDetails} EPFunction={EPFunction}/> : null}

                {
                    linkHash==="#3" ?
                        emailChanWarningDiaplayed===false ? <EmailChanWarning emailChanWarnDiaplUpdt={this.emailChanWarnDiaplUpdt} EPFunction={EPFunction}/> : <EditEmail email={email} EPFunction={EPFunction}/>
                    : null
                }

                {linkHash==="#10" ? <EditEmail email={email} EPFunction={EPFunction}/> : null}

                {linkHash==="#9" ? <EditProfilePix profile_pix={profile_pix} EPFunction={EPFunction}/> : null}

                {linkHash==="#4" ? <EditGender gender={gender} EPFunction={EPFunction}/> : null}

                {linkHash==="#5" ? <EditPassword EPFunction={EPFunction}/> : null}

                {linkHash==="#6" ? <EditAll firstName={firstName} lastName={lastName} EPFunction={EPFunction}/> : null}
                                
            </React.Fragment>
        );
    }
}

export default EditProfile;
           



class EditFirstName extends React.Component{
    constructor(props){
        super();
        this.state={
            firstName:props.firstName,
            login:props.EPFunction.login,
            fetchURL:props.EPFunction.fetchURL,
            redirectToProfileStatus:props.EPFunction.redirectToProfileStatus,
            f_nameErrMeg:"",
            pageErrMeg:""
        };
        this.onchangeHandler=this.onchangeHandler.bind(this);
        this.validation=this.validation.bind(this);
        this.submitFN=this.submitFN.bind(this);
    }

    submitFN(event){
        event.preventDefault();
        //api call
        //hide section and display loding
        document.getElementById('section').className="no_section";
        document.getElementById('section_waiting').className="section_waiting";
        document.getElementById('section_waiting_back').className="section_waiting_back";
        
        //api call
        const system=[
            navigator.productSub,
            navigator.appVersion,
            navigator.vendor,
            navigator.hardwareConcurrency,
            navigator.deviceMemory,
            navigator.hardwareConcurrency,
            navigator.language,
            navigator.platform
        ];
        const  systemJason=JSON.stringify(system);
    
        const body=new FormData();
        body.append("id", JSON.parse(sessionStorage.getItem("sessIdUse4"))[0]);
        body.append("loginTime", JSON.parse(sessionStorage.getItem("sessIdUse4"))[1]);
        body.append("system",systemJason);
        body.append("f_name",this.state.firstName);
        body.append("EditFirstName",true);

        const url=this.state.fetchURL+"edit_profile.php";
        const sender={
            method:"POST",
            body:body,
            headers:{
                Accept:"application/json",
            }
        };

        fetch(url, sender)
        .then(res=>{
            return res.json();
            // return res.text();
        })
        .then(res=>{
            // console.log(res);
            let error=res[0];
            let success=res[1];
            let otherErr=res[2];

            if(error['relogin']){
                //the below session will be used and erarzed on login page
                sessionStorage.setItem("reloginMeg", error['relogin']);
                sessionStorage.setItem("reloginLink", "login");
                sessionStorage.removeItem("sessIdUse4");
                this.props.EPFunction.deletAllState();
            }

            if(error['f_nameErrMeg']){
                this.setState({f_nameErrMeg:error['f_nameErrMeg']});
            }
            if(error['pageErrMeg']){
                this.setState({pageErrMeg:error['pageErrMeg']});
            }

            this.props.EPFunction.setOtherErr(otherErr);
            if(success["recordUpdstedMeg"]){
                sessionStorage.setItem("recordUpdstedMeg", success["recordUpdstedMeg"]);
                this.props.EPFunction.redirectToProfile();
            }
            document.getElementById('section').className="";
            document.getElementById('section_waiting').className="no_section";
            document.getElementById('section_waiting_back').className="no_section";    
        })
        .catch(res=>{
            let response;
            // console.log(res);
            if(typeof res==="object"){
                if(String(res)==="TypeError: Failed to fetch"){
                    if(navigator.onLine){
                        response="Connection failed, Please check your connection";
                    }else{
                        response="You are currently offline";
                    }
                }else{
                    response="Fatal Error, Please contact the admin";
                }
            }else{
                if(navigator.onLine){
                    response="Fatal Error, Please contact the admin";
                }else{
                    response="You are currently offline";
                }
            }
            this.setState({badErr:response});
            document.getElementById('section').className="";
            document.getElementById('section_waiting').className="no_section";
            document.getElementById('section_waiting_back').className="no_section";    
        });
    }

    static getDerivedStateFromProps(prevProps, state){
        if(state.redirectToProfileStatus!==prevProps.EPFunction.redirectToProfileStatus){
            let newState=prevProps.EPFunction.redirectToProfileStatus;
            return({redirectToProfileStatus:newState})
        }
        return({});
    }

    componentDidMount(){
        document.getElementById('firstName1').addEventListener("keyup", this.validation);
        document.getElementById('updateFN').addEventListener("click", this.submitFN);
        let updateFN=document.getElementById('updateFN');
        updateFN.setAttribute("disabled", "true");
    }

    validation(){
        //variables for elements
        let firstName=this.state.firstName;
        let f_nameErr=true;

        //firstnane variables
        let f_nameMeg=document.getElementById('f_nameMeg');
        let updateFN=document.getElementById('updateFN');        
            
        if(/[ ]/.test(firstName)===true){
            f_nameMeg.innerHTML="White space not allowed";
            f_nameMeg.className="errorMeg";
            f_nameErr=true;
        }else{
            f_nameMeg.innerHTML="";
            f_nameMeg.className="hide";
            f_nameErr=false;
        }

        if(f_nameErr===true || firstName===""){
            updateFN.setAttribute("disabled", "true");
        }else{
            updateFN.removeAttribute("disabled");
        }
    }

    onchangeHandler(event){
        let name=event.target.name;
        let value=event.target.value;
        this.setState({[name]:value});

        // remove backend error
        this.setState({
            f_nameErrMeg:"",
            pageErrMeg:""
        });
    }

    render(){
        const {firstName,redirectToProfileStatus, f_nameErrMeg, pageErrMeg}=this.state;
        
        return(
            <React.Fragment>
                {redirectToProfileStatus ? <Redirect to="profile"/> : null}

                {/* <!-- first name --> */}
                <form>
                    <div>
                        {pageErrMeg ? <p className='errorMeg'>{pageErrMeg}</p>: null}
                        <p className="formInputLabel">First Name: </p> <p className='formInput'> <input type="text" name="firstName" id="firstName1" placeholder="Enter your First Name" value={firstName} onChange={this.onchangeHandler} required/> </p>
                        <p id='f_nameMeg' className='hide'></p>
                        {f_nameErrMeg ? <p className='errorMeg'>{f_nameErrMeg}</p>: null}
                        <input type="submit" id="updateFN" value="Update"/>
                    </div>
                </form>
            </React.Fragment>
        );
    }
}



