import React from "react";
import {Link, Redirect} from "react-router-dom";


class Profile extends React.Component{

    constructor(props){
        super()
        this.state=props.details;
        this.state.otherErr="";
        this.state.badErr="";
        this.fetchApi=this.fetchApi.bind(this);
    }

    componentDidMount(){
        const {firstName,lastName,username,email,gender,profile_pix, emailVerified, login}=this.state;
        if((!firstName || !lastName || !username || !email || !gender || !profile_pix || emailVerified===null || sessionStorage.getItem("recordUpdstedMeg")) && login){
            this.fetchApi();
        }

        let currentPageName=window.location.pathname;
        this.props.updateFunct.updatePageName(currentPageName);

        setTimeout(
            ()=>{if(sessionStorage.getItem("recordUpdstedMeg")!==null){ sessionStorage.removeItem("recordUpdstedMeg");}},
            2000
        );

    }

    static getDerivedStateFromProps(prevProps, state){
        if(state!==prevProps.details){
            let newState=prevProps.details;
            return(newState);
        }      
        return({});
    }

    fetchApi(){
        //hide section and display loding
        document.getElementById('section').className="no_section";
        document.getElementById('section_waiting').className="section_waiting";
        document.getElementById('section_waiting_back').className="section_waiting_back";
        
        //api call
        const system=[
            navigator.productSub,
            navigator.appVersion,
            navigator.vendor,
            navigator.hardwareConcurrency,
            navigator.deviceMemory,
            navigator.hardwareConcurrency,
            navigator.language,
            navigator.platform
        ];
        const  systemJason=JSON.stringify(system);
    
        const body=new FormData();
        body.append("id", JSON.parse(sessionStorage.getItem("sessIdUse4"))[0]);
        body.append("loginTime", JSON.parse(sessionStorage.getItem("sessIdUse4"))[1]);
        body.append("system",systemJason);

        const url=this.state.fetchURL+"profile.php";
        const sender={
            method:"POST",
            body:body,
            headers:{
                Accept:"application/json",
            }
        };

        fetch(url, sender)
        .then(res=>{
            return res.json();
            // return res.text();
        })
        .then(res=>{
            // console.log(res);
            let error=res[0];
            let success=res[1];
            let otherErr=res[2];

            if(error['relogin']){
                //the below session will be used and erarzed on login page
                sessionStorage.setItem("reloginMeg", error['relogin']);
                sessionStorage.setItem("reloginLink", "login");
                sessionStorage.removeItem("sessIdUse4")
                this.setState({relogin:true});
                this.props.updateFunct.deletAllState();
            }

            this.setState({otherErr:otherErr});
            if(success["username"] && success["email"] && success["emailVerified"]){
                //if login sucess
                let newFirstName=success["firstName"];
                let newLastName=success["lastName"];
                let newUsername=success["username"];
                let newEmail=success["email"];
                let newGender=success["gender"];
                let newProfile_pix=success["profile_pix"];
                let newEmailVerified=success["emailVerified"];
                this.props.updateFunct.updateFromProfile(newFirstName,newLastName,newUsername,newEmail,newGender,newProfile_pix,newEmailVerified);
            }
            document.getElementById('section').className="";
            document.getElementById('section_waiting').className="no_section";
            document.getElementById('section_waiting_back').className="no_section";    
        })
        .catch(res=>{
            let response;
            // console.log(res);
            if(typeof res==="object"){
                if(String(res)==="TypeError: Failed to fetch"){
                    if(navigator.onLine){
                        response="Connection failed, Please check your connection";
                    }else{
                        response="You are currently offline";
                    }
                }else{
                    response="Fatal Error, Please contact the admin";
                }
            }else{
                if(navigator.onLine){
                    response="Fatal Error, Please contact the admin";
                }else{
                    response="You are currently offline";
                }
            }
            this.setState({badErr:response});
            document.getElementById('section').className="";
            document.getElementById('section_waiting').className="no_section";
            document.getElementById('section_waiting_back').className="no_section";    
        });
    }

    render(){
        const {
            login,
            logout,
            firstName,
            lastName,
            email,
            username,
            gender,
            profile_pix,       
            emailVerified,
            pPixURL,
            otherErr,
            badErr
        } = this.state;
        let profile_pixPath=pPixURL+profile_pix;
        let pixIsDefault;
        let emailHash;

        if(/default/.test(profile_pix)){pixIsDefault=true;}else{pixIsDefault=false;}

        emailVerified ? emailHash=3 : emailHash=10;

        return(
            <React.Fragment>
                {/* when not loged in */}
                {login ? null : <Redirect to="/"/>}
                {
                    login ?
                    null : logout ? null : sessionStorage.setItem("loginFirst", true)
                }
                {
                    login ?
                    null : logout ? null : sessionStorage.setItem("loginRedirect", this.state.currentPageName)
                }
                {
                    login ?
                    null : logout ? null : <Redirect to="login"/>
                } 
                {/* {otherErr ? <p className='errorMeg'>{otherErr}</p> : null} */}
                {otherErr.length ? <p className='errorMeg'>Fatal Error, Please contact the admin</p> : null}
                {badErr.length ? <p className='errorMeg'>Fatal Error, Please contact the admin</p> : null}
                {/* {badErr ? <p className='errorMeg'>{badErr}</p> : null} */}

                {sessionStorage.getItem("recordUpdstedMeg") ? <p className='meg'>{sessionStorage.getItem("recordUpdstedMeg")}</p>: null}

                {/* <!-- delete account --> */}
                <Link to="edit_profile#7" className="button_form del_acc">Delete Account</Link>  


                <div className="profileDiv">
                    <div id="PPContain">
                        <img src={profile_pixPath} alt="profile Pix" id="profile_pix"/> <span className="edit">
                                {pixIsDefault ? null : <Link to="edit_profile#8" id="linkprofile">delete</Link>}    
                                <Link to="edit_profile#9">{pixIsDefault ? 'add' : 'change'}</Link> 
                        </span>
                        <p>{username}</p>
                    </div>

                    <br/>

                    <Link to="edit_profile#6">edit profile</Link> 
                    
                    <h3>Account Information</h3>     
                    {/* <!-- first name --> */}
                    <p className="PLabel">First Name</p>
                    <span className="PValue">{firstName}</span> <span className="edit"> 
                        <Link to="edit_profile#1">{firstName ? 'edit' : 'Add First Name'}</Link>      
                    </span>
                    

                    {/* <!-- last name --> */}
                    <p className="PLabel">Last Name</p>
                    <span className="PValue">{lastName}</span> <span className="edit">
                        <Link to="edit_profile#2">{lastName ? 'edit' : 'Add Last Name'}</Link> 
                    </span>
                    
                    {/* <!-- email --> */}
                    <p className="PLabel">E-mail</p>
                    <span className="PValue">{email}</span> <span className="edit">
                            <Link to={`edit_profile#${emailHash}`}>change</Link>
                    </span>

                    {/* <!-- gender --> */}
                    <p className="PLabel">Gender</p>
                    <span className="PValue">{gender}</span> <span className="edit">
                        <Link to="edit_profile#4">{gender ? 'edit' : 'Add Gender Information'}</Link>  
                    </span>
                    
                    {/* <!-- password --> */}
                    <p className="PLabel">Password</p>
                    <span className="PValue">********</span> <span className="edit">
                            <Link to="edit_profile#5">edit</Link>  
                    </span>

                </div>

            </React.Fragment>
        );
    }
}

export default Profile;


