import React from "react";
import {Link, Redirect} from "react-router-dom";


class Home extends React.Component{

    constructor(props){
        super()
        this.state=props.details;
    }

    componentDidMount(){
        let currentPageName=window.location.pathname;
        this.props.updateFunct.updatePageName(currentPageName);
    }

    static getDerivedStateFromProps(prevProps, state){
        if(state!==prevProps.details){
            let newState=prevProps.details;
            return(newState);
        }      
        return({});
    }

    render(){
        const {
            login,
        }=this.state;

        let deletAllState=this.props.updateFunct.deletAllState;
        let updateFromHome=this.props.updateFunct.updateFromHome;

        return(
            <React.Fragment>
               {login ? 
                    <HomeLogedin details={this.state} deletAllState={deletAllState} updateFromHome={updateFromHome}/>:
                    <HomeLogedout deletAllState={deletAllState} />
                } 
               <br/><br/><br/><Link to='note'>View Note</Link>
            </React.Fragment>
        );
    }
}

export default Home;



class HomeLogedin extends React.Component{

    constructor(props){
        super()
        this.state=props.details;
        this.state.relogin=false;
        this.state.otherErr="";
        this.state.badErr="";
        this.fetchApi=this.fetchApi.bind(this);
    }

    componentDidMount(){
        const {username, email, emailVerified, login}=this.state;

        if((!username || !email || !emailVerified) && login){
            this.fetchApi();
        }

        //to unset all message sessions
        setTimeout(
            ()=>{
                if(sessionStorage.getItem("EVerMeg")!==null){ sessionStorage.removeItem("EVerMeg");}
                if(sessionStorage.getItem("EVerMeg2")!==null){ sessionStorage.removeItem("EVerMeg2");}
            },1500
        );
        //for account delete
        if(sessionStorage.getItem("userDelMg1")!==null){ 
            this.props.deletAllState();
        }

    }

    static getDerivedStateFromProps(prevProps, state){
        if(state!==prevProps.details){
            let newState=prevProps.details;
            return(newState);
        }      
        return({});
    }

    fetchApi(){
        //hide section and display loding
        document.getElementById('section').className="no_section";
        document.getElementById('section_waiting').className="section_waiting";
        document.getElementById('section_waiting_back').className="section_waiting_back";
        
        //api call
        const system=[
            navigator.productSub,
            navigator.appVersion,
            navigator.vendor,
            navigator.hardwareConcurrency,
            navigator.deviceMemory,
            navigator.hardwareConcurrency,
            navigator.language,
            navigator.platform
        ];
        const  systemJason=JSON.stringify(system);
    
        const body=new FormData();
        body.append("id", JSON.parse(sessionStorage.getItem("sessIdUse4"))[0]);
        body.append("loginTime", JSON.parse(sessionStorage.getItem("sessIdUse4"))[1]);
        body.append("system",systemJason);

        const url=this.state.fetchURL+"home.php";
        const sender={
            method:"POST",
            body:body,
            headers:{
                Accept:"application/json",
            }
        };

        fetch(url, sender)
        .then(res=>{
            return res.json();
            // return res.text();
        })
        .then(res=>{
            // console.log(res);
            let error=res[0];
            let success=res[1];
            let otherErr=res[2];

            if(error['relogin']){
                //the below session will be used and erarzed on login page
                sessionStorage.setItem("reloginMeg", error['relogin']);
                sessionStorage.setItem("reloginLink", "login");
                sessionStorage.removeItem("sessIdUse4")
                this.setState({relogin:true});
                this.props.deletAllState();
            }

            this.setState({otherErr:otherErr});
            if(success["username"] && success["email"] && success["emailVerified"]){
                //if login sucess
                let newUsername=success["username"];
                let newEmail=success["email"];
                let newEmailVerified=success["emailVerified"];
                this.props.updateFromHome(newUsername,newEmail,newEmailVerified);
            }
            document.getElementById('section').className="";
            document.getElementById('section_waiting').className="no_section";
            document.getElementById('section_waiting_back').className="no_section";    
        })
        .catch(res=>{
            let response;
            // console.log(res);
            if(typeof res==="object"){
                if(String(res)==="TypeError: Failed to fetch"){
                    if(navigator.onLine){
                        response="Connection failed, Please check your connection";
                    }else{
                        response="You are currently offline";
                    }
                }else{
                    response="Fatal Error, Please contact the admin";
                }
            }else{
                if(navigator.onLine){
                    response="Fatal Error, Please contact the admin";
                }else{
                    response="You are currently offline";
                }
            }
            this.setState({badErr:response});
            document.getElementById('section').className="";
            document.getElementById('section_waiting').className="no_section";
            document.getElementById('section_waiting_back').className="no_section";    
        });

        // username, email, EVeriStatus FROM user WHERE id=:id"
    }

    render(){
        
        const {
            emailVerified,
            email,
            username,
            otherErr,
            badErr
        }=this.state;
    
        return(
            <React.Fragment>
                {sessionStorage.getItem("EVerMeg") ? <p className='meg'>{sessionStorage.getItem("EVerMeg")}</p>: null}
                {sessionStorage.getItem("EVerMeg2") ? <p className='meg'>{sessionStorage.getItem("EVerMeg2")}</p>: null}
                {/* {otherErr ? <p className='errorMeg'>{otherErr}</p> : null} */}
                {otherErr.length ? <p className='errorMeg'>Fatal Error, Please contact the admin</p> : null}
                {badErr ? <p className='errorMeg'>{badErr}</p> : null}

                <h1>Home</h1>
                <p>Welcome {username}</p>

                {!emailVerified ? <EmailVerify email={email}/>:null}

                <br/>
                <p><Link to='profile'>View your profile</Link></p>

                <br/>
                <p><Link to='recep_list'>View Recipiant List</Link></p>
                <br/>
                <p><Link to='create_list'>Create Recipiant List</Link></p>
                <br/>
                <Link to='logout'>logout?</Link>

            </React.Fragment>
        );
    }
}




class HomeLogedout extends React.Component{

    componentDidMount(){
        //for account delete
        if(sessionStorage.getItem("userDelMg1")!==null){ 
            sessionStorage.clear();
            sessionStorage.setItem("userDelMg2", "You have successfully deleted your account");
            this.props.deletAllState();
        }
        //to unset all message sessions
        setTimeout(
            ()=>{
                if(sessionStorage.getItem("userDelMg2")!==null){ sessionStorage.removeItem("userDelMg2");}
                if(sessionStorage.getItem("EVerMeg")!==null){ sessionStorage.removeItem("EVerMeg");}
                if(sessionStorage.getItem("EVerMeg2")!==null){ sessionStorage.removeItem("EVerMeg2");}
            },1500
        );
    }

    render(){
        
        return(  
            <span>   
                {sessionStorage.getItem("EVerMeg") ? <p className='meg'>{sessionStorage.getItem("EVerMeg")}</p>: null}
                {sessionStorage.getItem("EVerMeg2") ? <p className='meg'>{sessionStorage.getItem("EVerMeg2")}</p>: null}
                {sessionStorage.getItem("userDelMg2") ? <p className='meg'>{sessionStorage.getItem("userDelMg2")}</p>: null}
                {sessionStorage.getItem("reloginLink") ? <Redirect to={sessionStorage.getItem("reloginLink")}/>: null}
                 
                <h1>Home</h1>     
                <Link to='login'>Login</Link> or <Link to='register'>Register</Link> 
            </span> 
        );
    }
}


class EmailVerify extends React.Component{

    render(){
        return(  
            <span>         
                <p>Your email is not verified <Link to={`verify_email`}>Verify now</Link></p>          
            </span> 
        );
    }
}




