import React from "react";
import {Link, Redirect} from "react-router-dom";

 
class ResetPassword extends React.Component{ 

    constructor(props){
        super()
        this.state={
            login:props.details.login,
            email:props.details.email,
            fetchURL:props.details.fetchURL,
            hashChecked:false,
            hashUsable:false,
            hashErr:"",
            errorMeg2:""
        }
        this.checkHash=this.checkHash.bind(this);
        this.setBadErr=this.setBadErr.bind(this);
        this.setOtherErr=this.setOtherErr.bind(this);
        this.setErrorMeg2=this.setErrorMeg2.bind(this);
    }

    checkHash(){
        //api call to check if hash is valid
        //set hashChecked and hashUsable
        //hide section and display loding
        document.getElementById('section').className="no_section";
        document.getElementById('section_waiting').className="section_waiting";
        document.getElementById('section_waiting_back').className="section_waiting_back";
        
        let hash=window.location.hash;
        hash=hash.split('#');
        let hash1=hash[1];
        let hash2=hash[2];
    
        const body=new FormData();
        body.append("id",hash1);
        body.append("token",hash2);
        body.append("checkHash",true);

        const url=this.state.fetchURL+"forgot_password.php";
        const sender={
            method:"POST",
            body:body,
            headers:{
                Accept:"application/json",
            }
        };

        fetch(url, sender)
        .then(res=>{
            return res.json();
            // return res.text();
        })
        .then(res=>{
            // console.log(res);
            let error=res[0];
            let success=res[1];
            let otherErr=res[2];

            if(error['errorMeg2']){
                this.setState({errorMeg2:error['errorMeg2']});
            }

            this.setState({otherErr:otherErr});
            if(success["hashUsable"]){
                this.setState({hashUsable:success["hashUsable"]});
            }else{
                this.setState({hashUsable:false});
            }
            document.getElementById('section').className="";
            document.getElementById('section_waiting').className="no_section";
            document.getElementById('section_waiting_back').className="no_section";    
        })
        .catch(res=>{
            let response;
            // console.log(res);
            if(typeof res==="object"){
                if(String(res)==="TypeError: Failed to fetch"){
                    if(navigator.onLine){
                        response="Connection failed, Please check your connection";
                    }else{
                        response="You are currently offline";
                    }
                }else{
                    response="Fatal Error, Please contact the admin";
                }
            }else{
                if(navigator.onLine){
                    response="Fatal Error, Please contact the admin";
                }else{
                    response="You are currently offline";
                }
            }
            this.setState({badErr:response});
            document.getElementById('section').className="";
            document.getElementById('section_waiting').className="no_section";
            document.getElementById('section_waiting_back').className="no_section";    
        });
    }

    componentDidMount(){
        let currentPageName=window.location.pathname;
        this.props.updateFunct.updatePageName(currentPageName);

        let hash=window.location.hash;
        if(hash){this.checkHash();}
    }

    static getDerivedStateFromProps(prevProps, state){
        if(state.login!==prevProps.details.login){
            let newLogin=prevProps.details.login;
            return({login:newLogin});
        }
        return({});
    }


    setBadErr(badErr){
        this.setState({badErr:badErr});
    }

    setOtherErr(otherErr){
        this.setState({otherErr:otherErr});
    }

    setErrorMeg2(errorMeg2){
        this.setState({errorMeg2:errorMeg2});
    }

    render(){
        const {hashUsable, login, otherErr, badErr, errorMeg2}=this.state;
        let hash=window.location.hash;
        let FPFunction={
            setBadErr:this.setBadErr,
            setOtherErr:this.setOtherErr,
            setErrorMeg2:this.setErrorMeg2
        };

        return(
            <React.Fragment>
                {login ? <Redirect to ='/'/> : null}
                {/* {otherErr ? <p className='errorMeg'>{otherErr}</p> : null} */}
                {otherErr.length ? <p className='errorMeg'>Fatal Error, Please contact the admin</p> : null}
                {badErr ? <p className='errorMeg'>{badErr}</p> : null}
                {errorMeg2 ? <p className='errorMeg'>{errorMeg2}</p> : null}
                {
                    hash ? 
                        hashUsable ?
                        <UseLink details={this.state} FPFunction={FPFunction}/> : <SendLink details={this.state} FPFunction={FPFunction}/>
                    : <SendLink details={this.state} FPFunction={FPFunction}/>
                }
            </React.Fragment>
        );
    }
}



export default ResetPassword;



class SendLink extends React.Component{
    constructor(props){
        super();
        this.state={
            email:props.details.email,
            fetchURL:props.details.fetchURL,
            meg:"",
            errorMeg:""
        }
        this.onchangeHandler=this.onchangeHandler.bind(this);
        this.submit=this.submit.bind(this);
    }

    submit(event){
        event.preventDefault();
        this.props.FPFunction.setErrorMeg2("");
        //api call to send email to user
        //hide section and display loding
        document.getElementById('section').className="no_section";
        document.getElementById('section_waiting').className="section_waiting";
        document.getElementById('section_waiting_back').className="section_waiting_back";
        
        //api to send email to api to statr verification process
        let ressetPassURL=window.location.href;
    
        const body=new FormData();
        body.append("email",this.state.email);
        body.append("ressetPassURL",ressetPassURL);
        body.append("SendLink",true);

        const url=this.state.fetchURL+"forgot_password.php";
        const sender={
            method:"POST",
            body:body,
            headers:{
                Accept:"application/json",
            }
        };

        fetch(url, sender)
        .then(res=>{
            return res.json();
            // return res.text();
        })
        .then(res=>{
            // console.log(res);
            let error=res[0];
            let success=res[1];
            let otherErr=res[2];

            if(error['errorMeg']){
                this.setState({errorMeg:error['errorMeg']});
            }

            this.props.FPFunction.setOtherErr(otherErr);
            if(success["meg"]){
                this.setState({meg:success["meg"]});
            }
            document.getElementById('section').className="";
            document.getElementById('section_waiting').className="no_section";
            document.getElementById('section_waiting_back').className="no_section";    
        })
        .catch(res=>{
            let response;
            // console.log(res);
            if(typeof res==="object"){
                if(String(res)==="TypeError: Failed to fetch"){
                    if(navigator.onLine){
                        response="Connection failed, Please check your connection";
                    }else{
                        response="You are currently offline";
                    }
                }else{
                    response="Fatal Error, Please contact the admin";
                }
            }else{
                if(navigator.onLine){
                    response="Fatal Error, Please contact the admin";
                }else{
                    response="You are currently offline";
                }
            }
            this.props.FPFunction.setBadErr(response);
            document.getElementById('section').className="";
            document.getElementById('section_waiting').className="no_section";
            document.getElementById('section_waiting_back').className="no_section";    
        });
    }

    componentDidMount(){
        document.getElementById('resetPass1').addEventListener('click', this.submit)
    }

    onchangeHandler(event){
        let name=event.target.name;
        let value=event.target.value;
        this.setState({[name]:value});

        // remove backend error
        this.setState({
            meg:"",
            errorMeg:""
        });
    }

    render(){
        const {email, errorMeg, meg}=this.state;
        return(
            <React.Fragment>
                {meg ? <p className='meg'>{meg}</p> : null}

                <form>
                    <div>
                        <p className="formInputLabel">Email:</p> <p className='formInput'> <input type='email' name='email' placeholder='Enter your account email' value={email} onChange={this.onchangeHandler} required/></p>
                        {errorMeg ? <p className='errorMeg'>{errorMeg}</p> : null}
                        
                        <input type='submit' id='resetPass1' value='Re-Set' />  <br/>
                        <p className='reg-or-log'><Link to='login'>Login</Link> or <Link to='register'>Register</Link> </p>
                    </div>

                </form>
            </React.Fragment>  
        );
    }
}



class UseLink extends React.Component{
    constructor(props){
        super();
        this.state={
            fetchURL:props.details.fetchURL,
            password:"",
            conPassword:"",
            conPassProbsolved:false,
            passwordSolved:false,
            showPasswordError:false,
            passwordMatchMessage:"",
            passwordErrorMessage:"",
            passwordErrorMessage2:"",
            passwordErrorMessage3:"",
            passwordErrorMessage4:"",
            errorMeg2:"",
            tologin:false
        }
        this.onchangeHandler=this.onchangeHandler.bind(this);
        this.onPasswordBlur=this.onPasswordBlur.bind(this);
        this.validation=this.validation.bind(this);
        this.submit=this.submit.bind(this);
    }

    submit(event){
        event.preventDefault();
        //api call to reset password
        //hide section and display loding
        document.getElementById('section').className="no_section";
        document.getElementById('section_waiting').className="section_waiting";
        document.getElementById('section_waiting_back').className="section_waiting_back";

        let hash=window.location.hash;
        hash=hash.split('#');
        let hash1=hash[1];
        let hash2=hash[2];
    
        const body=new FormData();
        body.append("id",hash1);
        body.append("token",hash2);
        body.append("password",this.state.password);
        body.append("conPassword",this.state.conPassword);
        body.append("UseLink",true);

        const url=this.state.fetchURL+"forgot_password.php";
        const sender={
            method:"POST",
            body:body,
            headers:{
                Accept:"application/json",
            }
        };

        fetch(url, sender)
        .then(res=>{
            return res.json();
            // return res.text();
        })
        .then(res=>{
            // console.log(res);
            let error=res[0];
            let success=res[1];
            let otherErr=res[2];

            if(error['passwordMatchMessage']){
                this.setState({passwordMatchMessage:error['passwordMatchMessage']});
            }
            if(error['passwordErrorMessage']){
                this.setState({passwordErrorMessage:error['passwordErrorMessage']});
            }
            if(error['passwordErrorMessage2']){
                this.setState({passwordErrorMessage2:error['passwordErrorMessage2']});
            }
            if(error['passwordErrorMessage3']){
                this.setState({passwordErrorMessage3:error['passwordErrorMessage3']});
            }
            if(error['passwordErrorMessage4']){
                this.setState({passwordErrorMessage4:error['passwordErrorMessage4']});
            }
            if(error['errorMeg2']){
                this.setState({errorMeg2:error['errorMeg2']});
            }

            this.props.FPFunction.setOtherErr(otherErr);
            if(success["passChangeMeg"]){
                sessionStorage.setItem("passChangeMeg", JSON.stringify(success));
                this.setState({tologin:true});
            }
            document.getElementById('section').className="";
            document.getElementById('section_waiting').className="no_section";
            document.getElementById('section_waiting_back').className="no_section";    
        })
        .catch(res=>{
            let response;
            // console.log(res);
            if(typeof res==="object"){
                if(String(res)==="TypeError: Failed to fetch"){
                    if(navigator.onLine){
                        response="Connection failed, Please check your connection";
                    }else{
                        response="You are currently offline";
                    }
                }else{
                    response="Fatal Error, Please contact the admin";
                }
            }else{
                if(navigator.onLine){
                    response="Fatal Error, Please contact the admin";
                }else{
                    response="You are currently offline";
                }
            }
            this.props.FPFunction.setBadErr(response);
            document.getElementById('section').className="";
            document.getElementById('section_waiting').className="no_section";
            document.getElementById('section_waiting_back').className="no_section";    
        });        
    }

    componentDidMount(){
        //sets onclik attribute to function view_UnView for the right elements
        document.getElementById('passwordRes1').addEventListener('blur', this.onPasswordBlur);
        document.getElementById('passwordRes1').addEventListener('keyup', this.validation);
        document.getElementById('passwordRes2').addEventListener('keyup', this.validation);
        document.getElementById('visibility1').addEventListener('click', this.viewUnView);
        document.getElementById('visibility2').addEventListener('click', this.viewUnView);
        document.getElementById('resetPass').addEventListener('click', this.submit);
        document.getElementById('resetPass').setAttribute('disabled', 'true');
    }

    onPasswordBlur(event){
        this.setState({showPasswordError:true});  
        this.validation(event);
    }

    validation(event){
        let name=event.target.name;
        let password=this.state.password;
        let conPassword=this.state.conPassword;
        let passwordID=document.getElementById('passwordRes1');
        let conPasswordID=document.getElementById('passwordRes2');
        let conPassMeg=document.getElementById('conPassMeg');
        let passMeg=document.getElementById('passMeg');
        let conPasswordSolved;
        let resetPass=document.getElementById('resetPass');

        if(name==="password"){
            //check if password meets riquirement
            let passwordSolved1=false, passwordSolved2=false, passwordSolved3=false, passwordSolved4=false;
            //check if password contains uppercase
            if(/[A-Z]/.test(password)===false){
                passwordSolved1=false;  
                passMeg.innerHTML="Password must contain uppercase";
            }else{
                passwordSolved1=true;
                passMeg.innerHTML="";
            }

            //check if password contains lowercase
            if(/[a-z]/.test(password)===false){
                passwordSolved2=false;  
                if(passMeg.innerHTML.length>0){passMeg.innerHTML=passMeg.innerHTML+"<br/>Password must contain lowercase"}else{passMeg.innerHTML="Password must contain lowercase"}
            }else{
                passwordSolved2=true;
                if(passMeg.innerHTML.length>0){passMeg.innerHTML=passMeg.innerHTML+""}else{passMeg.innerHTML=""}
            }

            //check if password contains number
            if(/[0-9]/.test(password)===false){
                passwordSolved3=false;  
                if(passMeg.innerHTML.length>0){passMeg.innerHTML=passMeg.innerHTML+"<br/>Password must contain number"}else{passMeg.innerHTML="Password must contain number"}
                }else{
                passwordSolved3=true;
                if(passMeg.innerHTML.length>0){passMeg.innerHTML=passMeg.innerHTML+""}else{passMeg.innerHTML=""}
            }

            //check if password contains number
            if(password.length<6){
                passwordSolved4=false;  
                if(passMeg.innerHTML.length>0){passMeg.innerHTML=passMeg.innerHTML+"<br/>Password must be at least 6 characters"}else{passMeg.innerHTML="Password must be at least 6 characters"}
            }else{
                passwordSolved4=true;
                if(passMeg.innerHTML.length>0){passMeg.innerHTML=passMeg.innerHTML+""}else{passMeg.innerHTML=""}
            }

            //display messages when needed
            if(password===""){passMeg.innerHTML="";}
            if(passMeg.innerHTML.length>0 && this.state.showPasswordError===true){passMeg.className="errorMeg";}else{passMeg.className="hide";}
        
            //keeps the solved values in a variable
            let passwordSolved=passwordSolved1&&passwordSolved2&&passwordSolved3&&passwordSolved4;
            //make passwordsolved a global variable
            this.setState({
                passwordSolved:passwordSolved
            });

            let conPasswordSolved;
            //to validate conPassword
            if(password!==conPassword){
                conPasswordSolved=false;
                conPassMeg.innerHTML="passwords do not match";
            }else{
                conPasswordSolved=true;
                conPassMeg.innerHTML="";
            }
            if(conPassword===""){conPassMeg.innerHTML="";}
            if(conPassMeg.innerHTML.length>0){conPassMeg.className="errorMeg"; conPasswordID.className="password wrongInput";}else{conPassMeg.className="hide"; conPasswordID.className="password";}
            
            //make conPassProbsolved global
            this.setState({conPasswordSolved:conPasswordSolved});
        }

        if(name==="conPassword"){        
            if(password!==conPassword){
                conPasswordSolved=false;
                conPassMeg.innerHTML="Passwords do not match";
            }else{
                conPasswordSolved=true;
                conPassMeg.innerHTML="";
            }

            if(conPassword===""){conPassMeg.innerHTML="";}
            if(conPassMeg.innerHTML.length>0){conPassMeg.className="errorMeg"; conPasswordID.className="password wrongInput";}else{conPassMeg.className="hide"; conPasswordID.className="password";}
            //make conPassProbsolved global
            this.setState({conPasswordSolved:conPasswordSolved});
        }

        if(passMeg.innerHTML.length>0 && this.state.showPasswordError===true){passMeg.className="errorMeg";}else{passMeg.className="hide";}
        //change the input class in case of error
        if(this.state.passwordSolved===false && this.state.showPasswordError===true){passwordID.className="password wrongInput";}else{passwordID.className="password";} 

        let probSolved=this.state.passwordSolved&&this.state.conPasswordSolved;
        if(probSolved===true){
            resetPass.removeAttribute("disabled");
        }else{
            resetPass.setAttribute("disabled", "true");
        }
    }

    onchangeHandler(event){
        let name=event.target.name;
        let value=event.target.value;
        this.setState({[name]:value});

        // remove backend error
        this.setState({
            passwordMatchMessage:"",
            passwordErrorMessage:"",
            passwordErrorMessage2:"",
            passwordErrorMessage3:"",
            passwordErrorMessage4:"",
            errorMeg2:""
        });
    }

    viewUnView(event){
        let visibility=event.target;
        let input=event.target.previousElementSibling;

        if(input.type==="password"){
            visibility.innerHTML="visibility"; 
            input.type="text"; 
        }else{
            visibility.innerHTML="visibility_off"; 
            input.type="password";
        } 
    }


    render(){
        const {
            password, 
            newPassword,
            passwordMatchMessage,
            passwordErrorMessage,
            passwordErrorMessage2,
            passwordErrorMessage3,
            passwordErrorMessage4,
            errorMeg2,
            tologin
        }=this.state;
        return(
            <React.Fragment>
                {tologin ? <Redirect to="login"/> : null}
                <form>
                    <div>
                        {errorMeg2 ? <p className='errorMeg'>{errorMeg2}</p> : null}

                        <input type='hidden' name='id'  value="<?php if(isset($_GET['id'])):echo $_GET['id']; endif; if(isset($_POST['id']) && !isset($_GET['id'])):echo $_POST['id']; endif;?>" required/>
                        <input type='hidden' name='token'  value="<?php if(isset($_GET['token'])):echo $_GET['token']; endif; if(isset($_POST['token']) && !isset($_GET['token'])):echo $_POST['token']; endif;?>" required/>
                        <p className="formInputLabel">New password:</p> <p className='formInput'> <input type='password' name='password' id='passwordRes1' placeholder='Enter your new password' value={password} onChange={this.onchangeHandler} required/><i className="material-icons centerVertically visibility" id="visibility1">visibility_off</i></p>
                        <p id='passMeg' className='hide'></p>
                        {passwordErrorMessage ? <p className='errorMeg'>{passwordErrorMessage}</p> : null}
                        {passwordErrorMessage2 ? <p className='errorMeg'>{passwordErrorMessage2}</p> : null}
                        {passwordErrorMessage3 ? <p className='errorMeg'>{passwordErrorMessage3}</p> : null}
                        {passwordErrorMessage4 ? <p className='errorMeg'>{passwordErrorMessage4}</p> : null}
                        {passwordMatchMessage && !passwordErrorMessage ? <p className='errorMeg'>{passwordMatchMessage}</p> : null}
                        <p className="formInputLabel">Confirm password:</p> <p className='formInput'> <input type='password' name='conPassword' id='passwordRes2' placeholder='Confirm your password' value={newPassword} onChange={this.onchangeHandler} required/><i className="material-icons centerVertically visibility" id="visibility2">visibility_off</i></p>
                        <p id='conPassMeg' className='hide'></p>
                        {passwordMatchMessage && !passwordErrorMessage ? <p className='errorMeg'>{passwordMatchMessage}</p> : null}

                        <input type='submit' id='resetPass' value='Change'/>  <br/>
                        <p className='reg-or-log'><Link to='login'>Login</Link> or <Link to='register'>Register</Link> </p>
                    
                    </div>
                </form>             
            </React.Fragment>  
        );
    }
}