import React from "react";
import logo from "../images/logo.png";
import {Link} from "react-router-dom";




class Header extends React.Component{

    constructor(props){
        super();
        this.state={
            login:props.details.login,  
            currentPageName:props.details.currentPageName
        };
    }

    static getDerivedStateFromProps(prevProps,state){

        if(state.login!==prevProps.details.login){
            let newLogin=prevProps.details.login;
            return(
                {login:newLogin}
            );
        }
        if(state.currentPageName!==prevProps.details.currentPageName){
            let newCurrentPageName=prevProps.details.currentPageName;
            return({currentPageName:newCurrentPageName});
        }
        return({});

    }

    render(){
        //destruct state
        const {login,currentPageName} = this.state;
        //login-out button
        let headLogoutInLink;
        let headLogoutInText;

        //program login-out button
        if(login===true){
            headLogoutInLink="/logout"
            headLogoutInText="Logout"
        }else if(login===false && currentPageName==="/login"){
            headLogoutInLink="/register"
            headLogoutInText="Register"
        }else{
            headLogoutInLink="login"
            headLogoutInText="Login"
        }

        return(
            // header
            <header>
                <img className='centerVertiHori logo' src={logo} alt="Logo" />

                <p className={`login-out header-login-out 
                    ${login ?'login-out-out': 'login-out-in'}`}>
                    <Link to={headLogoutInLink}> {headLogoutInText} </Link>
                </p>
                {/* <!-- logout/login button ends here --> */}
            </header>
        )
    }
}

export default Header;