import React from "react";
import ReactDom from "react-dom";
import {BrowserRouter as Router, Redirect} from "react-router-dom";
import "./resources/loginSystemV3.css";
//all includes
import Header from "./includes/header.js";
import Nav from "./includes/nav.js";
import Section from "./includes/section.js";
import Side from "./includes/side.js";
import Footer from "./includes/footer.js";




class Body extends React.Component{
    constructor(){
        super();

        this.state={
            login:sessionStorage.getItem("sessIdUse4") ? true : false,
            logout:false,
            firstName:"",
            lastName:"",
            email:"",
            username:"",
            gender:"",
            profile_pix:"",       
            emailVerified:null,
            currentPageName:window.location.pathname,
            // fetchURL:"http://localhost/sites/login%20system-v%203/apis/pages/",
            fetchURL:"http://femex.epizy.com/sites/login%20system-v%203/apis/pages/",
            // pdgURL:"http://localhost/sites/login%20system-v%203/apis/padges/",
            pdgURL:"http://femex.epizy.com/sites/login%20system-v%203/apis/padges/",
            // pPixURL:"http://localhost/sites/login%20system-v%203/profile_pix/",
            pPixURL:"http://femex.epizy.com/sites/login%20system-v%203/profile_pix/",

        };
        this.deletAllState=this.deletAllState.bind(this);
        this.updatePageName=this.updatePageName.bind(this); 
        this.updateFromHome=this.updateFromHome.bind(this); 
        this.updateFromProfile=this.updateFromProfile.bind(this); 
        this.login=this.login.bind(this);   
    }

    updatePageName(pageName){
        if(this.state.currentPageName!==pageName){
            this.setState({
                currentPageName:pageName
            });
        }
    }

    login(){
        this.setState({
            login:sessionStorage.getItem("sessIdUse4") ? true : false
        })
    }

    deletAllState(){
        sessionStorage.removeItem("sessIdUse4");
        this.setState({
            login:sessionStorage.getItem("sessIdUse4") ? true : false,
            logout:true,
            firstName:"",
            lastName:"",
            email:"",
            username:"",
            gender:"",
            profile_pix:"",       
            emailVerified:null
        });
    }

    updateFromHome(username,email,emailVerified){
        this.setState({
            email:email,
            username:username,
            emailVerified:parseInt(emailVerified)
        });
    }

    updateFromProfile(firstName,lastName,username,email,gender,profile_pix,emailVerified){
        this.setState({
            firstName:firstName,
            lastName:lastName,
            email:email,
            username:username,
            gender:gender,
            profile_pix:profile_pix,       
            emailVerified:parseInt(emailVerified)
        });
    }

    render(){
        let details=this.state;
        let updateFunct={
            login:this.login,
            updatePageName:this.updatePageName,
            deletAllState:this.deletAllState,
            updateFromHome:this.updateFromHome,
            updateFromProfile:this.updateFromProfile
        };

        let ppgaep=window.location.pathname;
        let ppgaepR;
        if(ppgaep==="/sites/login%20system-v%203_react/"){
            ppgaepR=true;
        }else{
            ppgaepR=false;
        }

        return(
            <React.Fragment>
                <Router>
                    {ppgaepR ? <Redirect to="/"/> : null}
                    <Header details={details}/>
                    <Nav details={details}/>
                    <Section updateFunct={updateFunct} details={details}/>
                    <Side/>
                    <Footer/>
                </Router>
            </React.Fragment>
        );
    }
}

ReactDom.render(<Body/>, document.getElementById('root'));
