import React from "react";
import {
    Link
} from "react-router-dom";

class Nav extends React.Component{

    constructor(props){
        super();
        this.state={
            login:props.details.login,  
            currentPageName:props.details.currentPageName
        };
        
    }

    // componentDidMount(){
    //     setTimeout(this.updateNav,5000);
    // }

    static getDerivedStateFromProps(prevProps, state){

        if(state.login!==prevProps.details.login){
            let newLogin=prevProps.details.login;
            return({login:newLogin});
        }
        if(state.currentPageName!==prevProps.details.currentPageName){
            let newCurrentPageName=prevProps.details.currentPageName;
            return({currentPageName:newCurrentPageName});
        }
        return({});
    }

    render(){
        //destruct state
        const {login,currentPageName}=this.state;
        //login-out button
        let navLogoutInLink;
        let navLogoutInText;

        //nav button css classes
        let nav1='notActiveBt';
        let nav2a='notActiveBt';
        let nav2b='notActiveBt';
        let nav3a='notActiveBt';
        let nav3b='notActiveBt';
        let nav4='notActiveBt';
        let nav5='notActiveBt';

        //program nav button css classed
        if(currentPageName==='/'){ nav1='activeBt';}
        else if(currentPageName==='/register'){ nav2a='activeBt';}
        else if(currentPageName==='/profile'){ nav2b='activeBt';}
        else if(currentPageName==='/login'){ nav3a='activeBt';}
        else if(currentPageName==='/recep_list'){ nav3b='activeBt';}
        else if(currentPageName==='/about'){ nav4='activeBt';}
        else if(currentPageName==='/contact'){ nav5='activeBt';}
        else{}
  
        //program login-out button
        if(login===true){
            navLogoutInLink="/logout"
            navLogoutInText="Logout"
        }else if(login===false && currentPageName==="/login"){
            navLogoutInLink="/register"
            navLogoutInText="Register"
        }else{
            navLogoutInLink="/login"
            navLogoutInText="Login"
        }

        //toggleable nav button link and programming
        let nav2Link;
        let nav3Link;
        if(login===true){
            nav2Link="/profile";
            nav3Link="/recep_list";
        }else{
            nav2Link="/register";
            nav3Link="/login";
        }

        return(    
            <nav>
                <ul className='centerVertically'>
                    <li><Link to="" className={nav1} >Home</Link></li>
                    <li><Link to={nav2Link} className={!login ? nav2a:nav2b} >{!login ? "Register":"Profile"}</Link></li>
                    <li><Link to={nav3Link} className={!login ? nav3a:nav3b} >{!login ? "Login":"Lists"}</Link></li>
                    <li><Link to="/about" className={nav4} >About</Link></li>
                    <li><Link to="/contact" className={nav5} >Contact me</Link></li>
                    {/* <!-- for login and logout button --> */}
                    <li className={`login-out ${login ?'login-out-out': 'login-out-in'}`}><Link to={navLogoutInLink}>{navLogoutInText}</Link></li> 
                    {/* <!-- logout/login button ends here --> */}

                </ul>
            </nav>
        )
    }

}

export default Nav;