import React from "react";
    
    
    
class Note extends React.Component{

    componentDidMount(){
        let currentPageName=window.location.pathname;
        this.props.updateFunct.updatePageName(currentPageName);
    }
    
    render(){
        return(
            <React.Fragment>
                <h2 className='centerH'>Login System Version 3</h2>
                <br/>

                <h3>About Login System Version 3</h3>
                <p>
                    Login System Version 3(React App), is a ReactJs version of the 
                    and <a target='_blank' href='http://femex.epizy.com/sites/login%20system-v%203/'>Login System Version 3</a> and my most prided project of my login series.
                    It has the exact functionnalities that gives the user more power over the user's account as the PHP version.<br/>
                    Languages used for this web app are HTML, CSS and ReactJS.
                </p>
                <br/>

                <h3>Languages used</h3>
                <ol>
                    <li>HTML</li>
                    <li>css</li>
                    <li>ReactJS</li>
                    <li>PHP(Backend)</li>
                    <li>JavaScript</li>
                </ol>
                <br/>

                <h3>Database used</h3>
                <ul>
                    <li>PHPMyAdmin</li>
                </ul>
                <br/>


                <h3>Features</h3>
                <ol>
                    <li>User can login with email or username</li>
                    <li>Immune to SQL injuction or XXS attack</li>
                    <li>Website is responsive till screen width of 212px</li>
                </ol>
                <br/>




                <h3>Form Validations</h3>
                <ul>
                    <li>Password must contain uppercase, lowercase, number and must be at least 6 characters</li>
                    <li>Username must contain atleast 4 characters</li>
                    <li>All fields must be filled</li>
                    <li>User can not submit untill all entries are validated</li>
                </ul>
                <br/>


                <h3>Form Features</h3>
                <ul>
                    <li>Fields can hold values if page reappears incase of submission error (except for password field)</li>
                    <li>Has good error message system</li>
                    <li>Login with email or username</li>
                    <li>Tells user if username or Email is used before submitting</li>
                </ul>
                <br/>

                <h3>Login system features</h3>
                <ul>
                    <li>User's profile page</li>
                    <li>Forgot password pagr</li>
                    <li>Email verification</li>
                    <li>Profile edit</li>
                    <li>Profile picture upload</li>
                </ul>
                <br/>
            </React.Fragment>
        );
    }

}



export default Note;