import React from "react";
import {Link, Redirect} from "react-router-dom";


class Register extends React.Component{

    constructor(props){
        super()
        this.state={
            login:props.details.login,
            fetchURL:props.details.fetchURL,
            prevPage:props.details.currentPageName,
            username:"",
            email:"",
            password:"",
            conPassword:"",
            showUsernameError:false, 
            showEmailError:false, 
            showPasswordError:false,
            usernameSolved:false, 
            emailSolved:false, 
            passwordSolved:false, 
            conPasswordSolved:false,
            tologin:false,
            otherErr:"",
            badErr:"",
            usernameTakenMessage:"",
            emailTakenMessage:"",
            emailTakenMessage2:"",
            emailTakenMessage3:"",
            passwordMatchMessage:"",
            usernameErrorMessage:"",
            usernameErrorMessage2:"",
            passwordErrorMessage:"",
            passwordErrorMessage2:"",
            passwordErrorMessage3:"",
            passwordErrorMessage4:"",
            fillFormMessage:"",
            fillFormMessage2:""
        }
        this.onchangeHandler=this.onchangeHandler.bind(this);
        this.onTargetBlur=this.onTargetBlur.bind(this);
        this.viewUnView=this.viewUnView.bind(this);
        this.validation=this.validation.bind(this);
        this.submit=this.submit.bind(this);
    }

    submit(event){
        event.preventDefault();
        //api call to register
        //hide section and display loding
        document.getElementById('section').className="no_section";
        document.getElementById('section_waiting').className="section_waiting";
        document.getElementById('section_waiting_back').className="section_waiting_back";
        
        //api call    
        const body=new FormData();
        body.append("email", this.state.email);
        body.append("username", this.state.username);
        body.append("password", this.state.password);
        body.append("conPassword", this.state.conPassword);

        const url=this.state.fetchURL+"register.php";
        const sender={
            method:"POST",
            body:body,
            headers:{
                Accept:"application/json",
            }
        };

        fetch(url, sender)
        .then(res=>{
            return res.json();
            // return res.text();
        })
        .then(res=>{
            // console.log(res);
            let error=res[0];
            let success=res[1];
            let otherErr=res[2];

            if(error['usernameTakenMessage']){
                this.setState({usernameTakenMessage:error['usernameTakenMessage']});
            }
            if(error['emailTakenMessage']){
                this.setState({emailTakenMessage:error['emailTakenMessage']});
            }
            if(error['emailTakenMessage2']){
                this.setState({emailTakenMessage2:error['emailTakenMessage2']});
            }
            if(error['emailTakenMessage3']){
                this.setState({emailTakenMessage3:error['emailTakenMessage3']});
            }
            if(error['passwordMatchMessage']){
                this.setState({passwordMatchMessage:error['passwordMatchMessage']});
            }
            if(error['usernameErrorMessage']){
                this.setState({usernameErrorMessage:error['usernameErrorMessage']});
            }
            if(error['usernameErrorMessage2']){
                this.setState({usernameErrorMessage2:error['usernameErrorMessage2']});
            }
            if(error['passwordErrorMessage']){
                this.setState({passwordErrorMessage:error['passwordErrorMessage']});
            }
            if(error['passwordErrorMessage2']){
                this.setState({passwordErrorMessage2:error['passwordErrorMessage2']});
            }
            if(error['passwordErrorMessage3']){
                this.setState({passwordErrorMessage3:error['passwordErrorMessage3']});
            }
            if(error['passwordErrorMessage4']){
                this.setState({passwordErrorMessage4:error['passwordErrorMessage4']});
            }
            if(error['fillFormMessage']){
                this.setState({fillFormMessage:error['fillFormMessage']});
            }
            if(error['fillFormMessage2']){
                this.setState({fillFormMessage2:error['fillFormMessage2']});
            }

            this.setState({otherErr:otherErr});
            if(success["regSuccessMessage"]){
                sessionStorage.setItem("regSuccessMessage", JSON.stringify(success));
                this.setState({tologin:true});
            }
            document.getElementById('section').className="";
            document.getElementById('section_waiting').className="no_section";
            document.getElementById('section_waiting_back').className="no_section";    
        })
        .catch(res=>{
            let response;
            // console.log(res);
            if(typeof res==="object"){
                if(String(res)==="TypeError: Failed to fetch"){
                    if(navigator.onLine){
                        response="Connection failed, Please check your connection";
                    }else{
                        response="You are currently offline";
                    }
                }else{
                    response="Fatal Error, Please contact the admin";
                }
            }else{
                if(navigator.onLine){
                    response="Fatal Error, Please contact the admin";
                }else{
                    response="You are currently offline";
                }
            }
            this.setState({badErr:response});
            document.getElementById('section').className="";
            document.getElementById('section_waiting').className="no_section";
            document.getElementById('section_waiting_back').className="no_section";    
        });
    }

    componentDidMount(){
        let currentPageName=window.location.pathname;
        this.props.updateFunct.updatePageName(currentPageName);

        document.getElementById('visibility1').addEventListener("click", this.viewUnView);
        document.getElementById('visibility2').addEventListener("click", this.viewUnView);

        let username=document.getElementById('username');
        let email=document.getElementById('email');
        let password=document.getElementById('password');
        let conPassword=document.getElementById('conPassword');
        let regSubmitBtn=document.getElementById('regSubmitBtn');

        email.addEventListener("keyup", this.validation);
        username.addEventListener("keyup", this.validation);
        password.addEventListener("keyup", this.validation);
        conPassword.addEventListener("keyup", this.validation);
        email.addEventListener("blur", this.onTargetBlur);
        username.addEventListener("blur", this.onTargetBlur);
        password.addEventListener("blur", this.onTargetBlur);
        regSubmitBtn.addEventListener("click", this.submit);
        regSubmitBtn.setAttribute("disabled", "true");
        
    }

    onTargetBlur(event){
        let name=event.target.name;
        if(name==="email"){this.setState({showEmailError:true});}
        if(name==="username"){this.setState({showUsernameError:true});}
        if(name==="password"){this.setState({showPasswordError:true});} 
        this.validation(event);   
    }

    validation(event){
        let name=event.target.name;
        //store tages in variabled
        let username=this.state.username;
        let usernameID=document.getElementById('username');
        let email=this.state.email;
        let emailID=document.getElementById('email');
        let password=this.state.password;
        let passwordID=document.getElementById('password');
        let conPassword=this.state.conPassword;
        let conPasswordID=document.getElementById('conPassword');
        let regSubmitBtn=document.getElementById('regSubmitBtn');

        //messages
        let unameMeg=document.getElementById('unameMeg');
        let emailMeg=document.getElementById('emailMeg');
        let passMeg=document.getElementById('passMeg');
        let conPassMeg=document.getElementById('conPassMeg');

        if(name==="username"){
            //check for username with ajax and check for other problems
            //local username problem variable
            let usernameSolved2=false, usernameSolved3=false;
            //error message for used username
            //let usernameChosenMeg=document.getElementById('checkUsername');
            //check if username input is greater that 3,  for the ajax stuff
            // if(username.length <= 3 || username===" "){
            //     usernameChosenMeg.innerHTML="";
            // }

            //check if username is up to 4 chracters
            if(username.length<4){
                usernameSolved2=false;
                unameMeg.innerHTML="Username must be at least 4 characters";
            }else{
                usernameSolved2=true;
                unameMeg.innerHTML="";
            }

            //chech if username  contain space
            if(/[ ]/.test(username)===true){
                if(unameMeg.innerHTML.length>0){unameMeg.innerHTML=unameMeg.innerHTML+"<br/>White space not allowed in username";}else{unameMeg.innerHTML="White space not allowed in username";}
                usernameSolved3=false;
            }else{
                usernameSolved3=true;
            }

            //check if username is empty and displays messages when needed
            if(username===""){
                unameMeg.innerHTML="";
            }

            if(unameMeg.innerHTML.length>0 && this.state.showUsernameError===true){unameMeg.className="errorMeg";}else{unameMeg.className="hide";}
            let usernameSolved=usernameSolved2&&usernameSolved3;
            this.setState({usernameSolved:usernameSolved});
        }

        //check if there is an error and onblure is true for username
        //then display the message
        if(unameMeg.innerHTML.length>0 && this.state.showUsernameError===true){unameMeg.className="errorMeg";}else{unameMeg.className="hide";}
        //change the input class in case of error
        if(this.state.usernameSolved===false && this.state.showUsernameError===true){usernameID.className="wrongInput";}else{usernameID.className="";}

        if(name==="email"){
            //holds data to be send asyn
            //var data="email="+email;
            //local emailcheck variables
            let emailSolved1=false, emailSolved3=false;
            //let emailRegMeg=document.getElementById('checkEmail');

            //url for the ajaxChecks.php
            //let ajaxChecskUrl="/sites/login system-v 2/resources/ajaxChecks.php";
            //
            //check if email can be said to be valid
            if(/([@].{2,})/.test(email)===true && /([\.][a-zA-Z]{2,})$/.test(email)===true && /^(.{2,}[@])/.test(email)===true){
                emailSolved1=true;

            }else{
                //emailRegMeg.innerHTML="";
                //emailRegMeg.className="hide";
                emailMeg.innerHTML="Please enter a valid email";
                emailSolved1=false;
            }
        
            //check for white space
            if(/[ ]/.test(email)===true){
                if(emailMeg.innerHTML.length>0){emailMeg.innerHTML=emailMeg.innerHTML+"<br/>White space not allowed in email";}else{emailMeg.innerHTML="White space not allowed in email";}
                emailSolved3=false;
                }else{
                emailSolved3=true;
            }
        
            //other email checks
            //set message to nothing on empty input
            if(email===""){emailMeg.innerHTML="";}
            //display message if available
            if(emailMeg.innerHTML.length>0 && this.state.showEmailError===true){emailMeg.className="errorMeg";}else{emailMeg.className="hide";}
            let emailSolved=emailSolved1&&emailSolved3;
            this.setState({emailSolved:emailSolved});
        }

        //check if there is an error and onblure is true for email
        //then display the message
        if(emailMeg.innerHTML.length>0 && this.state.showEmailError===true){emailMeg.className="errorMeg";}else{emailMeg.className="hide";}
        //change the input class in case of error
        if(this.state.emailSolved===false && this.state.showEmailError===true){emailID.className="wrongInput";}else{emailID.className="";}

        if(name==="password"){
            let passwordSolved1=false, passwordSolved2=false, passwordSolved3=false, passwordSolved4=false;
            //check if password contains uppercase
            if(/[A-Z]/.test(password)===false){
                passwordSolved1=false;  
                passMeg.innerHTML="Password must contain uppercase";
                }else{
                passwordSolved1=true;
                passMeg.innerHTML="";
            }

            //check if password contains lowercase
            if(/[a-z]/.test(password)===false){
                passwordSolved2=false;  
                if(passMeg.innerHTML.length>0){passMeg.innerHTML=passMeg.innerHTML+"<br/>Password must contain lowercase"}else{passMeg.innerHTML="Password must contain lowercase"}
                }else{
                passwordSolved2=true;
                if(passMeg.innerHTML.length>0){passMeg.innerHTML=passMeg.innerHTML+""}else{passMeg.innerHTML=""}
            }

            //check if password contains number
            if(/[0-9]/.test(password)===false){
                passwordSolved3=false;  
                if(passMeg.innerHTML.length>0){passMeg.innerHTML=passMeg.innerHTML+"<br/>Password must contain number"}else{passMeg.innerHTML="Password must contain number"}
                }else{
                passwordSolved3=true;
                if(passMeg.innerHTML.length>0){passMeg.innerHTML=passMeg.innerHTML+""}else{passMeg.innerHTML=""}
            }

            //check if password contains number
            if(password.length<6){
                passwordSolved4=false;  
                if(passMeg.innerHTML.length>0){passMeg.innerHTML=passMeg.innerHTML+"<br/>Password must be at least 6 characters"}else{passMeg.innerHTML="Password must be at least 6 characters"}
                }else{
                passwordSolved4=true;
                if(passMeg.innerHTML.length>0){passMeg.innerHTML=passMeg.innerHTML+""}else{passMeg.innerHTML=""}
            }

            //display messages when needed
            if(password===""){passMeg.innerHTML="";}
            if(passMeg.innerHTML.length>0 && this.state.showPasswordError===true){passMeg.className="errorMeg";}else{passMeg.className="hide";}
        
            //keeps the solved values in a variable
            let passwordSolved=passwordSolved1&&passwordSolved2&&passwordSolved3&&passwordSolved4;
            //make passwordsolved a global variable
            this.setState({passwordSolved:passwordSolved});

            let conPasswordSolved;
            //to validate conPassword
            if(password!==conPassword){
                conPasswordSolved=false;
                conPassMeg.innerHTML="passwords do not match";
            }else{
                conPasswordSolved=true;
                conPassMeg.innerHTML="";
            }
            if(conPassword===""){conPassMeg.innerHTML="";}
            if(conPassMeg.innerHTML.length>0){conPassMeg.className="errorMeg"; conPasswordID.className="password wrongInput";}else{conPassMeg.className="hide"; conPasswordID="password";}
            this.setState({conPasswordSolved:conPasswordSolved});
        }

        //check everytime if there is an error and onblure is true for password
        //then display the message
        if(passMeg.innerHTML.length>0 && this.state.showPasswordError===true){passMeg.className="errorMeg";}else{passMeg.className="hide";}
        //change the input class in case of error
        if(this.state.passwordSolved===false && this.state.showPasswordError===true){passwordID.className="password wrongInput";}else{passwordID.className="password";} 

        if(name==="conPassword"){
            let conPasswordSolved;
            if(password!==conPassword){
                conPasswordSolved=false;
                conPassMeg.innerHTML="passwords do not match";
            }else{
                conPasswordSolved=true;
                conPassMeg.innerHTML="";
            }

            if(conPassword===""){conPassMeg.innerHTML="";}
            if(conPassMeg.innerHTML.length>0){conPassMeg.className="errorMeg"; conPasswordID.className="password wrongInput";}else{conPassMeg.className="hide"; conPasswordID.className="password";}
            this.setState({conPasswordSolved:conPasswordSolved});
        }
        
        //check if all problems are solved, the enable the rgister button
        //then display the message
        let probSolved=this.state.usernameSolved&&this.state.emailSolved&&this.state.passwordSolved&&this.state.conPasswordSolved;
        if(probSolved===true){regSubmitBtn.removeAttribute("disabled");}else{regSubmitBtn.setAttribute("disabled", "true");}
    }

    viewUnView(event){
        let visibility=event.target;
        let input=event.target.previousElementSibling;

        if(input.type==="password"){
            visibility.innerHTML="visibility"; 
            input.type="text"; 
        }else{
            visibility.innerHTML="visibility_off"; 
            input.type="password";
        } 
    }

    onchangeHandler(event){
        let name=event.target.name;
        let value=event.target.value;
        this.setState({[name]:value});

        // remove backend error
        this.setState({
            usernameTakenMessage:"",
            emailTakenMessage:"",
            emailTakenMessage2:"",
            emailTakenMessage3:"",
            passwordMatchMessage:"",
            usernameErrorMessage:"",
            usernameErrorMessage2:"",
            passwordErrorMessage:"",
            passwordErrorMessage2:"",
            passwordErrorMessage3:"",
            passwordErrorMessage4:"",
            fillFormMessage:"",
            fillFormMessage2:""
        });
    }

    render(){
        const {
            login, 
            prevPage, 
            username, 
            email, 
            password, 
            conPassword,
            otherErr,
            badErr,
            tologin,
            usernameTakenMessage,
            emailTakenMessage,
            emailTakenMessage2,
            emailTakenMessage3,
            passwordMatchMessage,
            usernameErrorMessage,
            usernameErrorMessage2,
            passwordErrorMessage,
            passwordErrorMessage2,
            passwordErrorMessage3,
            passwordErrorMessage4,
            fillFormMessage,
            fillFormMessage2
        }=this.state;
        let pp=prevPage;
        if(prevPage==="/register" || !prevPage){pp="/";}
        let ALLpasswordErrorMessage=passwordErrorMessage&&passwordErrorMessage2&&passwordErrorMessage3&&passwordErrorMessage4;
        return(
            <React.Fragment>
                {tologin ? <Redirect to="login"/> : null}
                {login ? <Redirect to={pp}/> : null}
                {/* {otherErr ? <p className='errorMeg'>{otherErr}</p> : null} */}
                {otherErr.length ? <p className='errorMeg'>Fatal Error, Please contact the admin</p> : null}
                {badErr.length ? <p className='errorMeg'>Fatal Error, Please contact the admin</p> : null}
                {/* {badErr ? <p className='errorMeg'>{badErr}</p> : null} */}
                {fillFormMessage ? <p className='errorMeg'>{fillFormMessage}</p> : null}
                {fillFormMessage2 ? <p className='errorMeg'>{fillFormMessage2}</p> : null}

                <form>
                    <div>
                        {/* <!-- username --> */}
                        <p className="formInputLabel">Username: </p> <p className='formInput'> <input type="text" name="username" id="username" placeholder="Enter your prefared Username" onChange={this.onchangeHandler} value={username} required/> </p>
                        <p id='checkUsername' className='hide'></p>
                        <p id='unameMeg' className='hide'></p>
                        {usernameTakenMessage ? <p className='errorMeg'>{usernameTakenMessage}</p> : null}
                        {usernameErrorMessage ? <p className='errorMeg'>{usernameErrorMessage}</p> : null}
                        {usernameErrorMessage2 ? <p className='errorMeg'>{usernameErrorMessage2}</p> : null}

                        {/* <!-- email --> */}
                        <p className="formInputLabel">E-mail: </p> <p className='formInput'> <input type="email" name="email" id="email" placeholder="Enter your email" onChange={this.onchangeHandler} value={email} required/> </p>
                        <p id='checkEmail' className='hide'></p>
                        <p id='emailMeg' className='hide'></p>
                        {emailTakenMessage ? <p className='errorMeg'>{emailTakenMessage}</p> : null}
                        {emailTakenMessage2 ? <p className='errorMeg'>{emailTakenMessage2}</p> : null}
                        {emailTakenMessage3 ? <p className='errorMeg'>{emailTakenMessage3}</p> : null}

                        {/* <!-- password --> */}
                        <p className="formInputLabel">Password: </p> <p className='formInput'> <input type="password" className="password" name="password" id="password" onChange={this.onchangeHandler} value={password} placeholder="Enter your prefared password"  required /> <i className="material-icons centerVertically visibility" id="visibility1">visibility_off</i></p>
                        <p id='passMeg' className='hide'></p>
                        {passwordErrorMessage ? <p className='errorMeg'>{passwordErrorMessage}</p> : null}
                        {passwordErrorMessage2 ? <p className='errorMeg'>{passwordErrorMessage2}</p> : null}
                        {passwordErrorMessage3 ? <p className='errorMeg'>{passwordErrorMessage3}</p> : null}
                        {passwordErrorMessage4 ? <p className='errorMeg'>{passwordErrorMessage4}</p> : null}
                        {passwordMatchMessage && !ALLpasswordErrorMessage ? <p className='errorMeg'>{passwordMatchMessage}</p> : null}

                        {/* <!-- conpassword --> */}
                        <p className="formInputLabel">Confirm Password: </p> <p className='formInput'> <input type="password" className="password" name="conPassword" id="conPassword" onChange={this.onchangeHandler} value={conPassword} placeholder="Confirm your password"  required/> <i className="material-icons centerVertically visibility" id="visibility2">visibility_off</i></p>
                        <p id='conPassMeg' className='hide'></p>
                        {passwordMatchMessage && !ALLpasswordErrorMessage ? <p className='errorMeg'>{passwordMatchMessage}</p> : null}

                        <input type="submit" value="Register" id="regSubmitBtn"/> <br/>
                        <p className='reg-or-log'> or <Link to='login'>Login</Link> </p>
                    </div>
                </form>
            </React.Fragment>
        );
    }
}

export default Register;


