import React from "react";
import {Link, Redirect} from "react-router-dom";


class RecepList extends React.Component{

    constructor(props){
        super();
        this.state=props.details;
        this.state.otherErr=""; 
        this.state.badErr=""; 
        this.state.success=null; 
        this.state.delSucs=""; 
        this.state.delFailed=""; 
        this.state.del=false; 

        this.fetchApi=this.fetchApi.bind(this);
        this.deleteCol=this.deleteCol.bind(this);
        this.fillTableById_listTable=this.fillTableById_listTable.bind(this);
    }

    componentDidMount(){
        let currentPageName=window.location.pathname;
        this.props.updateFunct.updatePageName(currentPageName);

        if(this.state.login )(this.fetchApi());
        if(this.state.del )(this.fetchApi());
    }

    fetchApi(){
        // remove backend error
        this.setState({
            otherErr:"",
            badErr:"",
            delSucs:"",
            delFailed:""
        });
        
        //hide section and display loding
        document.getElementById('section').className="no_section";
        document.getElementById('section_waiting').className="section_waiting";
        document.getElementById('section_waiting_back').className="section_waiting_back";
        
        //api call
        const system=[
            navigator.productSub,
            navigator.appVersion,
            navigator.vendor,
            navigator.hardwareConcurrency,
            navigator.deviceMemory,
            navigator.hardwareConcurrency,
            navigator.language,
            navigator.platform
        ];
        const  systemJason=JSON.stringify(system);
    
        const body=new FormData();
        body.append("id", JSON.parse(sessionStorage.getItem("sessIdUse4"))[0]);
        body.append("loginTime", JSON.parse(sessionStorage.getItem("sessIdUse4"))[1]);
        body.append("system",systemJason);

        const url=this.state.pdgURL+"recep_list.php";
        const sender={
            method:"POST",
            body:body,
            headers:{
                Accept:"application/json",
            }
        };

        fetch(url, sender)
        .then(res=>{
            return res.json();
            // return res.text();
        })
        .then(res=>{
            // console.log(res);
            let error=res[0];
            let success=res[1];
            let otherErr=res[2];

            if(error['relogin']){
                //the below session will be used and erarzed on login page
                sessionStorage.setItem("reloginMeg", error['relogin']);
                sessionStorage.setItem("reloginLink", "login");
                sessionStorage.removeItem("sessIdUse4")
                this.props.updateFunct.deletAllState();
            }

            this.setState({otherErr:otherErr});
            if(success[0]){
                this.setState({success:success});
            }
            if(success["empty"]){
                this.setState({success:success});
            }
            this.fillTableById_listTable();
            document.getElementById('section').className="";
            document.getElementById('section_waiting').className="no_section";
            document.getElementById('section_waiting_back').className="no_section";    
        })
        .catch(res=>{
            let response;
            // console.log(res);
            if(typeof res==="object"){
                if(String(res)==="TypeError: Failed to fetch"){
                    if(navigator.onLine){
                        response="Connection failed, Please check your connection";
                    }else{
                        response="You are currently offline";
                    }
                }else{
                    response="Fatal Error, Please contact the admin";
                }
            }else{
                if(navigator.onLine){
                    response="Fatal Error, Please contact the admin";
                }else{
                    response="You are currently offline";
                }
            }
            this.setState({badErr:response});
            document.getElementById('section').className="";
            document.getElementById('section_waiting').className="no_section";
            document.getElementById('section_waiting_back').className="no_section";    
        });
    }

    deleteCol(event){
        event.preventDefault();
        // remove backend error
        this.setState({
            otherErr:"",
            badErr:"",
            delSucs:"",
            delFailed:""
        });

        //hide section and display loding
        document.getElementById('section').className="no_section";
        document.getElementById('section_waiting').className="section_waiting";
        document.getElementById('section_waiting_back').className="section_waiting_back";
        
        //api call
        const system=[
            navigator.productSub,
            navigator.appVersion,
            navigator.vendor,
            navigator.hardwareConcurrency,
            navigator.deviceMemory,
            navigator.hardwareConcurrency,
            navigator.language,
            navigator.platform
        ];
        const  systemJason=JSON.stringify(system);

        let name=event.target.name;
        let colName=name.split(":")[1];
    
        const body=new FormData();
        body.append("id", JSON.parse(sessionStorage.getItem("sessIdUse4"))[0]);
        body.append("loginTime", JSON.parse(sessionStorage.getItem("sessIdUse4"))[1]);
        body.append("system",systemJason);
        body.append("colName",colName);
        body.append("deleteCol",true);

        const url=this.state.pdgURL+"recep_list.php";
        const sender={
            method:"POST",
            body:body,
            headers:{
                Accept:"application/json",
            }
        };

        fetch(url, sender)
        .then(res=>{
            return res.json();
            // return res.text();
        })
        .then(res=>{
            // console.log(res);
            let error=res[0];
            let success=res[1];
            let otherErr=res[2];
            let success2=res[3];

            if(error['relogin']){
                //the below session will be used and erarzed on login page
                sessionStorage.setItem("reloginMeg", error['relogin']);
                sessionStorage.setItem("reloginLink", "login");
                sessionStorage.removeItem("sessIdUse4")
                this.props.updateFunct.deletAllState();
            }
            if(error['delFailed']){
                this.setState({delFailed:error['delFailed']});
            }
            this.setState({otherErr:otherErr});
            if(success2["delSucs"]){
                this.setState({delSucs:success2["delSucs"]});
            }
            if(success[0]){
                this.setState({success:success});
            }
            if(success["empty"]){
                this.setState({success:success});
            }
            this.fillTableById_listTable();
            document.getElementById('section').className="";
            document.getElementById('section_waiting').className="no_section";
            document.getElementById('section_waiting_back').className="no_section";    
        })
        .catch(res=>{
            let response;
            // console.log(res);
            if(typeof res==="object"){
                if(String(res)==="TypeError: Failed to fetch"){
                    if(navigator.onLine){
                        response="Connection failed, Please check your connection";
                    }else{
                        response="You are currently offline";
                    }
                }else{
                    response="Fatal Error, Please contact the admin";
                }
            }else{
                if(navigator.onLine){
                    response="Fatal Error, Please contact the admin";
                }else{
                    response="You are currently offline";
                }
            }
            this.setState({badErr:response});
            document.getElementById('section').className="";
            document.getElementById('section_waiting').className="no_section";
            document.getElementById('section_waiting_back').className="no_section";    
        });
    }

    fillTableById_listTable(){
        let table=document.getElementById('listTable');
        let content =table.getElementsByTagName('tr');
        if(content.length){
            while(content.length){
                content[0].remove();
            }
        }

        let success=this.state.success;
        if(success[0]){
            success.forEach(d => {
                let tr = document.createElement("tr");
                let td1 = document.createElement("td");
                let td2 = document.createElement("td");
                let td3 = document.createElement("td");
                let input = document.createElement("input");
                input.type='submit';
                input.className='button_form';
                input.name='colName:'+d['title'];
                input.value='Delete';
                input.addEventListener("click", this.deleteCol);

                let td1con = document.createTextNode(d['title']);
                let td2con = document.createTextNode(d['count']);

                td1.appendChild(td1con);
                td2.appendChild(td2con);
                td3.appendChild(input);
                tr.appendChild(td1);
                tr.appendChild(td2);
                tr.appendChild(td3);
                table.appendChild(tr);
            })
        }
        if(success['empty']){
            let tr = document.createElement("tr");
            let td = document.createElement("td");
            let text=document.createTextNode(success['empty']);
            td.colSpan=3;
            td.appendChild(text);
            tr.appendChild(td);
            table.appendChild(tr);
        }
    }

    static getDerivedStateFromProps(prevProps, state){
        if(state.login!==prevProps.details.login){
            let newLogin=prevProps.details.login;
            return({login:newLogin});
        }
        return({});
    }

    render(){
        const {login, logout, otherErr, badErr, delSucs, delFailed}=this.state;
        // remove backend error
        return(
            <React.Fragment>
                {/* {otherErr ? <p className='errorMeg'>{otherErr}</p> : null} */}
                {otherErr.length ? <p className='errorMeg'>Fatal Error, Please contact the admin</p> : null}
                {badErr ? <p className='errorMeg'>{badErr}</p> : null}
                
                {/* when not loged in */}
                {login ? null : <Redirect to="/"/>}
                {
                    login ?
                    null : logout ? null : sessionStorage.setItem("loginFirst", true)
                }
                {
                    login ?
                    null : logout ? null : sessionStorage.setItem("loginRedirect", this.state.currentPageName)
                }
                {
                    login ?
                    null : logout ? null : <Redirect to="login"/>
                } 

                <Link to='/create_list'><input type='button' value='Create A Recepient List'/></Link>

                <h2>Manage Your List Easily</h2>
                <p>This page was created to act as an email list management system</p>
                {delSucs ? <p className='meg'>{delSucs}</p> : null}
                {delFailed ? <p className='meg'>{delFailed}</p> : null}
                <table>
                    <thead>
                        <tr><th>Title</th><th>Recepients</th><th>Action</th></tr>
                    </thead>
                    <tbody id="listTable">
                    </tbody>
                </table>
            </React.Fragment>
        );
    }
}
    
   
export default RecepList;