import React from "react";
import {Redirect} from "react-router-dom";


class VerifyEmail extends React.Component{

    constructor(props){
        super()
        this.state={
            email:props.details.email,
            fetchURL:props.details.fetchURL,
            emailVerified:props.details.emailVerified,
            showEmailError:false,
            emailSolved:false,
            otherErr:"",
            badErr:"",
            errorMeg:"",
            errorMeg2:"",
            tohome:false
        }
        this.onchangeHandler=this.onchangeHandler.bind(this);
        this.onEmailBlur=this.onEmailBlur.bind(this);
        this.validation=this.validation.bind(this);
        this.tryVarify=this.tryVarify.bind(this);
        this.submit=this.submit.bind(this);
    }

    submit(event){
        event.preventDefault()
        //hide section and display loding
        document.getElementById('section').className="no_section";
        document.getElementById('section_waiting').className="section_waiting";
        document.getElementById('section_waiting_back').className="section_waiting_back";
        
        //api to send email to api to statr verification process
        let emailverifyURL=window.location.href;
    
        const body=new FormData();
        body.append("email",this.state.email);
        body.append("emailverifyURL",emailverifyURL);
        body.append("VerifyEmail",true);

        const url=this.state.fetchURL+"email_verify.php";
        const sender={
            method:"POST",
            body:body,
            headers:{
                Accept:"application/json",
            }
        };

        fetch(url, sender)
        .then(res=>{
            return res.json();
            // return res.text();
        })
        .then(res=>{
            // console.log(res);
            let error=res[0];
            let success=res[1];
            let otherErr=res[2];

            if(error['errorMeg']){
                this.setState({errorMeg:error['errorMeg']});
            }

            this.setState({otherErr:otherErr});
            if(success["EVerMeg"]){
                sessionStorage.setItem("EVerMeg", success["EVerMeg"]);
                if(success["EVerMeg2"]){sessionStorage.setItem("EVerMeg2", success["EVerMeg2"]);}
                this.setState({tohome:true});
            }
            document.getElementById('section').className="";
            document.getElementById('section_waiting').className="no_section";
            document.getElementById('section_waiting_back').className="no_section";    
        })
        .catch(res=>{
            let response;
            // console.log(res);
            if(typeof res==="object"){
                if(String(res)==="TypeError: Failed to fetch"){
                    if(navigator.onLine){
                        response="Connection failed, Please check your connection";
                    }else{
                        response="You are currently offline";
                    }
                }else{
                    response="Fatal Error, Please contact the admin";
                }
            }else{
                if(navigator.onLine){
                    response="Fatal Error, Please contact the admin";
                }else{
                    response="You are currently offline";
                }
            }
            this.setState({badErr:response});
            document.getElementById('section').className="";
            document.getElementById('section_waiting').className="no_section";
            document.getElementById('section_waiting_back').className="no_section";    
        });
    }

    tryVarify(){
        //hide section and display loding
        document.getElementById('section').className="no_section";
        document.getElementById('section_waiting').className="section_waiting";
        document.getElementById('section_waiting_back').className="section_waiting_back";
        
        //get the hashes and send to api
        let hash=window.location.hash;
        hash=hash.split('#');
        let hash1=hash[1];
        let hash2=hash[2];
        //send hash one and two to api
        //api to send email to api to statr verification process
    
        const body=new FormData();
        body.append("id",hash1);
        body.append("token",hash2);
        body.append("tryVarify",true);

        const url=this.state.fetchURL+"email_verify.php";
        const sender={
            method:"POST",
            body:body,
            headers:{
                Accept:"application/json",
            }
        };

        fetch(url, sender)
        .then(res=>{
            return res.json();
            // return res.text();
        })
        .then(res=>{
            // console.log(res);
            let error=res[0];
            let success=res[1];
            let otherErr=res[2];

            if(error['errorMeg2']){
                this.setState({errorMeg2:error['errorMeg2']});
            }

            this.setState({otherErr:otherErr});
            if(success["EVerMeg"]){
                sessionStorage.setItem("EVerMeg", success["EVerMeg"]);
                if(success["EVerMeg2"]){sessionStorage.setItem("EVerMeg2", success["EVerMeg2"]);}
                this.setState({tohome:true});
            }
            document.getElementById('section').className="";
            document.getElementById('section_waiting').className="no_section";
            document.getElementById('section_waiting_back').className="no_section";    
        })
        .catch(res=>{
            let response;
            // console.log(res);
            if(typeof res==="object"){
                if(String(res)==="TypeError: Failed to fetch"){
                    if(navigator.onLine){
                        response="Connection failed, Please check your connection";
                    }else{
                        response="You are currently offline";
                    }
                }else{
                    response="Fatal Error, Please contact the admin";
                }
            }else{
                if(navigator.onLine){
                    response="Fatal Error, Please contact the admin";
                }else{
                    response="You are currently offline";
                }
            }
            this.setState({badErr:response});
            document.getElementById('section').className="";
            document.getElementById('section_waiting').className="no_section";
            document.getElementById('section_waiting_back').className="no_section";    
        });
    }

    componentDidMount(){
        let currentPageName=window.location.pathname;
        this.props.updateFunct.updatePageName(currentPageName);

        let email=document.getElementById('email');
        let veriEmailBtn=document.getElementById('veriEmail');

        email.addEventListener("keyup", this.validation);
        veriEmailBtn.addEventListener("click", this.submit);
        
        let hash=window.location.hash;
        if(hash){this.tryVarify()}

        //determine if some error message shows for the email
        email.addEventListener("blur", this.onEmailBlur);
    }

    static getDerivedStateFromProps(prevProps, state){
        if(state.login!==prevProps.details.login){
            let newLogin=prevProps.details.login;
            return({login:newLogin});
        }
        return({});
    }

    //determine if some error message shows for the email
    onEmailBlur(event){
        this.setState({showEmailError:true})
        this.validation(event);
    }

    validation(){
        let email=this.state.email;
        let emailID=document.getElementById('email');
        let emailMeg=document.getElementById('emailMeg');
        let veriEmailBtn=document.getElementById('veriEmail');
        
        let emailSolved=false, emailSolved1=false, emailSolved2=false //, emailSolved3=false;
            
        //check if email can be said to be valid
        if(/([@].{2,})/.test(email)===true && /([\.][a-zA-Z]{2,})$/.test(email)===true && /^(.{2,}[@])/.test(email)===true){
            emailSolved1=true;
            emailMeg.innerHTML="";
            //store xmlhttprequest in variable
            }else{                
            emailMeg.innerHTML="Please enter a valid email";
            emailSolved1=false;
        }
    
        //check for white space
        if(/[ ]/.test(email)===true){
            if(emailMeg.innerHTML.length>0){emailMeg.innerHTML=emailMeg.innerHTML+"<br/>White space not allowed in email";}else{emailMeg.innerHTML="White space not allowed in email";}
            emailSolved2=false;
        }else{
            emailSolved2=true;
        }
    
        //other email checks
        //set message to nothing on empty input
        if(email===""){emailMeg.innerHTML="";}
        //display message if available
        if(emailMeg.innerHTML.length>0 && this.state.showEmailError===true){emailMeg.className="errorMeg";}else{emailMeg.className="hide";}
   
        emailSolved=emailSolved1&&emailSolved2;
        this.setState({emailSolved:emailSolved});

        //check everytime if there is an error and onblure is true for email
        //then display the message
        if(emailMeg.innerHTML.length>0 && this.state.showEmailError===true){emailMeg.className="errorMeg";}else{emailMeg.className="hide";}
        //change the input class in case of error
        if(this.state.emailSolved===false && this.state.showEmailError===true){emailID.className="wrongInput";}else{emailID.className="";}
        if(this.state.emailSolved===false || email===""){
            veriEmailBtn.setAttribute("disabled", "true");
        }else{
            veriEmailBtn.removeAttribute("disabled");
        }
    }

    onchangeHandler(event){
        let name=event.target.name;
        let value=event.target.value;
        this.setState({[name]:value});

        // remove backend error
        this.setState({
            otherErr:"",
            badErr:"",
            errorMeg:"",
            errorMeg2:""
        });
    }

    render(){
        const {
            email, 
            otherErr, 
            badErr, 
            errorMeg, 
            errorMeg2,
            tohome
        }=this.state;
        return(
            <React.Fragment>
                {tohome ? <Redirect to="/"/> : null}
                {/* {otherErr ? <p className='errorMeg'>{otherErr}</p> : null} */}
                {otherErr.length ? <p className='errorMeg'>Fatal Error, Please contact the admin</p> : null}
                {badErr ? <p className='errorMeg'>{badErr}</p> : null}

                <form>
                    <div>
                        {/* <!-- email --> */}
                        <p className="formInputLabel">E-mail: </p> <p className='formInput'> <input type="email" name="email" id="email" onChange={this.onchangeHandler} placeholder="Enter your email" value={email} required/> </p>
                        {errorMeg ? <p className='errorMeg'>{errorMeg}</p> : null}
                        {errorMeg2 ? <p className='errorMeg'>{errorMeg2}</p> : null}
                        <p id='emailMeg' className='hide'></p>   
                    <input type="submit" id="veriEmail" value="Verify Email"/> <br/>
                    </div>
            
                </form>
            </React.Fragment>
        );
    }
}

export default VerifyEmail;
