import React from "react";
import {Redirect} from "react-router-dom";
  
  
class CreateList extends React.Component{

    constructor(props){
        super();
        this.state=props.details;
        this.state.contactListName=""; 
        this.state.dropzoneData=""; 
        this.state.contactListString=""; 
        this.state.otherErr=""; 
        this.state.badErr="";
        this.state.CLNErrMeg="";
        this.state.CLFErrMeg="";
        this.state.CLSErrMeg="";
        this.state.insertedMeg="";

        this.saveListApi=this.saveListApi.bind(this);
        this.dropzoneOndrop=this.dropzoneOndrop.bind(this);
        this.delFileBtnOnclick=this.delFileBtnOnclick.bind(this);
        this.choosefileOnchange=this.choosefileOnchange.bind(this);
        this.onchangeHandler=this.onchangeHandler.bind(this);
    }

    saveListApi(event){
        event.preventDefault();
        //hide section and display loding
        document.getElementById('section').className="no_section";
        document.getElementById('section_waiting').className="section_waiting";
        document.getElementById('section_waiting_back').className="section_waiting_back";
        
        //api call
        const system=[
            navigator.productSub,
            navigator.appVersion,
            navigator.vendor,
            navigator.hardwareConcurrency,
            navigator.deviceMemory,
            navigator.hardwareConcurrency,
            navigator.language,
            navigator.platform
        ];
        const  systemJason=JSON.stringify(system);
    
        const body=new FormData();
        body.append("id", JSON.parse(sessionStorage.getItem("sessIdUse4"))[0]);
        body.append("loginTime", JSON.parse(sessionStorage.getItem("sessIdUse4"))[1]);
        body.append("system",systemJason);
        body.append("dropzoneData",this.state.dropzoneData);
        body.append("contactListName",this.state.contactListName);
        body.append("contactListString",this.state.contactListString);
        body.append("saveList",true);

        const url=this.state.pdgURL+"create_list.php";
        const sender={
            method:"POST",
            body:body,
            headers:{
                Accept:"application/json",
            }
        };

        fetch(url, sender)
        .then(res=>{
            return res.json();
            // return res.text();
        })
        .then(res=>{
            // console.log(res);
            let error=res[0];
            let success=res[1];
            let otherErr=res[2];

            if(error['relogin']){
                //the below session will be used and erarzed on login page
                sessionStorage.setItem("reloginMeg", error['relogin']);
                sessionStorage.setItem("reloginLink", "login");
                sessionStorage.removeItem("sessIdUse4")
                this.props.updateFunct.deletAllState();
            }
            if(error['CLNErrMeg']){
                this.setState({CLNErrMeg:error['CLNErrMeg']});
            }
            if(error['CLFErrMeg']){
                this.setState({CLFErrMeg:error['CLFErrMeg']});
            }
            if(error['CLSErrMeg']){
                this.setState({CLSErrMeg:error['CLSErrMeg']});
            }

            this.setState({otherErr:otherErr});
            if(success["insertedMeg"]){
                this.setState({
                    insertedMeg:success['insertedMeg'],
                    contactListName:"", 
                    dropzoneData:"", 
                    contactListString:""
                });
                let dropzone=document.getElementById('dropzone');
                let uptext1=document.getElementById('uptext1');
                let uptext2=document.getElementById('uptext2');
                let choosefile=document.getElementById('choosefile');
                let dropstatus=document.getElementById('dropstatus');
                let p1=document.getElementById('p1');
                let p2=document.getElementById('p2');
                let changeFileBtn=document.getElementById('changeFileBtn');
                let delFileBtn=document.getElementById('delFileBtn');

                dropzone.className="dropzone";
                p1.className="formInputLabel";
                p2.className="formInput";
                uptext1.className="centerHH";
                uptext2.className="dragtext";
                choosefile.className="";
                dropstatus.className="nodisplay";
                changeFileBtn.className="nodisplay";
                delFileBtn.className="nodisplay";
                choosefile.value="";
            }
            this.fillTableById_listTable();
            document.getElementById('section').className="";
            document.getElementById('section_waiting').className="no_section";
            document.getElementById('section_waiting_back').className="no_section";    
        })
        .catch(res=>{
            let response;
            // console.log(res);
            if(typeof res==="object"){
                if(String(res)==="TypeError: Failed to fetch"){
                    if(navigator.onLine){
                        response="Connection failed, Please check your connection";
                    }else{
                        response="You are currently offline";
                    }
                }else{
                    response="Fatal Error, Please contact the admin";
                }
            }else{
                if(navigator.onLine){
                    response="Fatal Error, Please contact the admin";
                }else{
                    response="You are currently offline";
                }
            }
            this.setState({badErr:response});
            document.getElementById('section').className="";
            document.getElementById('section_waiting').className="no_section";
            document.getElementById('section_waiting_back').className="no_section";    
        });
    }

    componentDidMount(){
        let currentPageName=window.location.pathname;
        this.props.updateFunct.updatePageName(currentPageName);

        let dropzone=document.getElementById('dropzone');
        let changeFileBtn=document.getElementById('changeFileBtn');
        let delFileBtn=document.getElementById('delFileBtn');
        let choosefile=document.getElementById('choosefile');
        let saveList=document.getElementById('saveList');

        saveList.addEventListener("click", this.saveListApi);
        dropzone.addEventListener("drop", this.dropzoneOndrop);
        dropzone.addEventListener("dragover", this.dropzoneOndragover);
        dropzone.addEventListener("dragleave", this.dropzoneOndragleave);
        delFileBtn.addEventListener("click", this.delFileBtnOnclick);
        changeFileBtn.addEventListener("click", this.changeFileBtnOnclick);
        choosefile.addEventListener("change", this.choosefileOnchange);
    }

    static getDerivedStateFromProps(prevProps, state){
        if(state.login!==prevProps.details.login){
            let newLogin=prevProps.details.login;
            return({login:newLogin});
        }
        return({});
    }

    delFileBtnOnclick(){
        // remove backend messages
        this.setState({
            CLNErrMeg:"",
            CLFErrMeg:"",
            CLSErrMeg:"",
            insertedMeg:""
        });
        
        let dropzone=document.getElementById('dropzone');
        let uptext1=document.getElementById('uptext1');
        let uptext2=document.getElementById('uptext2');
        let choosefile=document.getElementById('choosefile');
        let dropstatus=document.getElementById('dropstatus');
        let p1=document.getElementById('p1');
        let p2=document.getElementById('p2');
        let changeFileBtn=document.getElementById('changeFileBtn');
        let delFileBtn=document.getElementById('delFileBtn');

        dropzone.className="dropzone";
        p1.className="formInputLabel";
        p2.className="formInput";
        uptext1.className="centerHH";
        uptext2.className="dragtext";
        choosefile.className="";
        dropstatus.className="nodisplay";
        changeFileBtn.className="nodisplay";
        delFileBtn.className="nodisplay";
        choosefile.value="";
        this.setState({dropzoneData:""});
    }

    changeFileBtnOnclick(){
        let dropzone=document.getElementById('dropzone');
        let uptext1=document.getElementById('uptext1');
        let uptext2=document.getElementById('uptext2');
        let choosefile=document.getElementById('choosefile');
        let dropstatus=document.getElementById('dropstatus');
        let p1=document.getElementById('p1');
        let p2=document.getElementById('p2');
        let changeFileBtn=document.getElementById('changeFileBtn');

        dropzone.className="dropzone";
        p1.className="formInputLabel";
        p2.className="formInput";
        uptext1.className="centerHH";
        uptext2.className="dragtext";
        choosefile.className="";
        dropstatus.className="nodisplay";
        changeFileBtn.className="nodisplay";
    }

    dropzoneOndrop(event){
        // remove backend messages
        this.setState({
            CLNErrMeg:"",
            CLFErrMeg:"",
            CLSErrMeg:"",
            insertedMeg:""
        });

        event.preventDefault();
        //variables
        let dropzone=document.getElementById('dropzone');
        let uptext1=document.getElementById('uptext1');
        let uptext2=document.getElementById('uptext2');
        let choosefile=document.getElementById('choosefile');
        let dropstatus=document.getElementById('dropstatus');
        let p1=document.getElementById('p1');
        let p2=document.getElementById('p2');
        let changeFileBtn=document.getElementById('changeFileBtn');
        let delFileBtn=document.getElementById('delFileBtn');
        let saveList=document.getElementById('saveList');
        let filTypErr=document.getElementById('filTypErr');

        let ext=String(event.dataTransfer.files[0].name.split(".").slice(-1));
        if(ext==='txt' || ext==='csv'){
            let reader=new FileReader();
            reader.onload=()=>{
                this.setState({dropzoneData:reader.result});
                p1.className="nodisplay";
                p2.className="nodisplay";
                uptext1.className="nodisplay";
                uptext2.className="nodisplay";
                choosefile.className="nodisplay";
                dropstatus.className="centerH status";
                changeFileBtn.className="";
                delFileBtn.className="";
                saveList.removeAttribute("disabled")
            }
            reader.readAsText(event.dataTransfer.files[0]);
            filTypErr.className="nodisplay";
            filTypErr.innerHTML="";
        }else{
            this.setState({dropzoneData:""})
            saveList.setAttribute("disabled","true")
            filTypErr.className="errorMeg";
            filTypErr.innerHTML="Only .txt and .csv files are allowed";
        }  
        dropstatus.innerHTML= event.dataTransfer.files[0].name+" is droped";
        dropzone.className="dropzone";
    }

    dropzoneOndragover(event){
        event.preventDefault();

        let dropzone=document.getElementById('dropzone');
        let uptext1=document.getElementById('uptext1');
        let uptext2=document.getElementById('uptext2');
        let choosefile=document.getElementById('choosefile');

        dropzone.className="dropzonehover";
        uptext1.className="nodisplay";
        uptext2.className="";
        choosefile.className="nodisplay";
        return false;
    }

    dropzoneOndragleave(){
        let dropzone=document.getElementById('dropzone');
        let uptext1=document.getElementById('uptext1');
        let uptext2=document.getElementById('uptext2');
        let choosefile=document.getElementById('choosefile');

        dropzone.className="dropzone";
        uptext1.className="centerHH";
        uptext2.className="dragtext";
        choosefile.className="";
        return false;
    }

    choosefileOnchange(){
        // remove backend messages
        this.setState({
            CLNErrMeg:"",
            CLFErrMeg:"",
            CLSErrMeg:"",
            insertedMeg:""
        });

        let uptext1=document.getElementById('uptext1');
        let uptext2=document.getElementById('uptext2');
        let choosefile=document.getElementById('choosefile');
        let dropstatus=document.getElementById('dropstatus');
        let dropzoneData=document.getElementById('dropzoneData');
        let p1=document.getElementById('p1');
        let p2=document.getElementById('p2');
        let changeFileBtn=document.getElementById('changeFileBtn');
        let delFileBtn=document.getElementById('delFileBtn');
        let saveList=document.getElementById('saveList');
        let filTypErr=document.getElementById('filTypErr');

        let ext=String(choosefile.files[0].name.split(".").slice(-1));
        if(ext==='txt' || ext==='csv'){
            let reader=new FileReader();
            reader.onload=()=>{
                this.setState({dropzoneData:reader.result});
                p1.className="nodisplay";
                p2.className="nodisplay";
                uptext1.className="nodisplay";
                uptext2.className="nodisplay";
                choosefile.className="nodisplay";
                dropstatus.className="centerH status";
                dropstatus.innerHTML= choosefile.files[0].name+" is selected";
                changeFileBtn.className="";
                delFileBtn.className="";
            }
            reader.readAsText(choosefile.files[0]);
            filTypErr.className="nodisplay";
            filTypErr.innerHTML="";
        }else{
            dropzoneData.value="";
            saveList.setAttribute("disabled","true")
            filTypErr.className="errorMeg";
            filTypErr.innerHTML="Only .txt and .csv files are allowed";
        }
    }

    onchangeHandler(event){
        let name=event.target.name;
        let value=event.target.value;
        this.setState({[name]:value});
    }

    render(){
        const {
            login, 
            logout, 
            dropzoneData,
            contactListName,
            contactListString,
            otherErr, 
            badErr,
            CLNErrMeg,
            CLFErrMeg,
            CLSErrMeg,
            insertedMeg
        }=this.state;
        return(
            <React.Fragment>
                {/* {otherErr ? <p className='errorMeg'>{otherErr}</p> : null} */}
                {otherErr.length ? <p className='errorMeg'>Fatal Error, Please contact the admin</p> : null}
                {badErr ? <p className='errorMeg'>{badErr}</p> : null}
                {/* when not loged in */}
                {login ? null : <Redirect to="/"/>}
                {
                    login ?
                    null : logout ? null : sessionStorage.setItem("loginFirst", true)
                }
                {
                    login ?
                    null : logout ? null : sessionStorage.setItem("loginRedirect", this.state.currentPageName)
                }
                {
                    login ?
                    null : logout ? null : <Redirect to="login"/>
                } 

                <p>This page was created to act as an email list management system</p>
                <form>
                    {insertedMeg ? <p className='meg'>{insertedMeg}</p> : null}
                    {/* <?php if(!empty($insertedMeg)): echo "<p className='meg'>".$insertedMeg."<p>"; endif; ?> */}
                    <div>
                        <p className="formInputLabel">Contact List Name: </p> 
                        <p className='formInput'> 
                            <input type="text" name="contactListName" placeholder="Enter list name" value={contactListName} onChange={this.onchangeHandler} required/> 
                        </p>
                        {CLNErrMeg ? <p className='errorMeg'>{CLNErrMeg}</p> : null}

                        <p className="formInputLabel">Upload List:</p>
                        <div className="dropzone" id="dropzone">
                            <span id="dropstatus" className="nodisplay">status</span>
                            <p className="formInputLabel" id="p1"> 
                                <span id="uptext2" className="dragtext">Drang and drop</span>
                                <span style={{width:"100%"}} id="uptext1" className="centerHH">Or</span>
                            </p> 
                            <p className='formInput' id="p2"> 
                                <input id="choosefile" type="file" name="contactListFile" placeholder="Drag and drop a file" accept=".csv, .txt" /> 
                            </p> <input type="button" className="nodisplay" id="changeFileBtn" value="Change File"/>
                            <input type="button" className="nodisplay" id="changeFileBtn" value="ChangeFile"/>
                            <input type="button" className="nodisplay" id="delFileBtn" value="Delete File"/>
                        </div>
                        <p className="nodisplay" id="filTypErr"></p>
                        {CLFErrMeg ? <p className='errorMeg'>{CLFErrMeg}</p> : null}
                        <input type="hidden" name="dropzoneData" id="dropzoneData" value={dropzoneData} onChange={this.onchangeHandler}/>
                        {/* <input type="text" name="dropzoneData" id="dropzoneData" value={dropzoneData} onChange={this.onchangeHandler}/> */}

                        <p className="formInputLabel">Or Enter Manually: </p>  
                        <textarea name="contactListString" placeholder="Enter Email id seperated by comma" onChange={this.onchangeHandler}
                            value={contactListString}
                        />
                        {CLSErrMeg ? <p className='errorMeg'>{CLSErrMeg}</p> : null}

                        <input type="submit" name="saveList" value="Save list" id="saveList"/> <br/>
                    </div>
                    {insertedMeg ? <p className='meg'>{insertedMeg}</p> : null}
                    {/* <?php if(!empty($insertedMeg)): echo "<p className='meg'>".$insertedMeg."<p>"; endif; ?> */}

                </form>
            </React.Fragment>
        );
    }
}

export default CreateList;
