import React from "react";
import {Redirect} from "react-router-dom";


class Logout extends React.Component{
    constructor(props){
        super();
        this.state={
            rPage:props.rPage
        }
    }

    componentDidMount(){
        this.props.deletAllState();
    }

    render(){
        let rPage=this.state.rPage;
        let page;
        if(rPage){
            page=rPage;
        }else{
            page="/";
        }

        return(
            <React.Fragment>
                <Redirect to={page} />
           </React.Fragment>
        );
        
    }
}


export default Logout;