import React from "react";
import {
    Switch,
    Route,
} from "react-router-dom";
//all pages
import ResetPassword from "../pages/forgot_password.js";
import EditProfile from "../pages/edit_profile.js";
import VerifyEmail from "../pages/email_verify.js";
import CreateList from "../padges/create_list.js";
import RecepList from "../padges/recep_list.js";
import Register from "../pages/register.js";
import Contact from "../pages/contact.js";
import Profile from "../pages/profile.js";
import Login from "../pages/login.js";
import About from "../pages/about.js";
import Note from "../pages/note.js";
import Home from "../pages/home.js";
import Logout from "../resources/logout.js";

class Section extends React.Component {

    constructor(props){
        super();
        this.state=props.details;
    }

    static getDerivedStateFromProps(prevProps, state){
        if(state!==prevProps.details){
            let newstate=prevProps.details;
            return(newstate);
        }
        return({});
    }

    render(){
        let details=this.state;
        let rPage=this.state.currentPageName;

        return (  
            <React.Fragment>
                {/* section_waiting_back,section_waiting,no_section */}
                <div id="section_waiting_back" className="no_section">
                </div>  
                {/* loader startes here */}
                <div id="section_waiting" className="no_section">
                    {rPage!=="/login" && rPage!=="/register" ?
                        <div className="loader_contain loader_conLoad">
                            <span className="text textLoad centerVertically"><h1>Loading</h1></span>
                            <span className="loader"></span>
                        </div>
                        : null
                    }
                    {rPage==="/login" ?
                        <div className="loader_contain loader_conLog">
                            <span className="text textLog centerVertically"><h1>Loging In</h1></span>
                            <span className="loader"></span>
                        </div>
                        : null
                    }
                    {rPage==="/register" ?
                        <div className="loader_contain loader_conPros">
                            <span className="text textPros centerVertically"><h1>Processing</h1></span>
                            <span className="loader"></span>
                        </div>
                        : null
                    }
                </div>  
                {/* loader ends here */}
                <section id="section" className="">

                    <Switch>
                        <Route path="/" exact>
                            <Home updateFunct={this.props.updateFunct} details={details}/>
                        </Route>
                        <Route path="/about" exact>
                            <About updateFunct={this.props.updateFunct}/>
                        </Route>
                        <Route path="/edit_profile" exact>
                            <EditProfile updateFunct={this.props.updateFunct} details={this.state}/>
                        </Route>
                        <Route path="/contact" exact>
                            <Contact updateFunct={this.props.updateFunct}/>
                        </Route>
                        <Route path="/note" exact>
                            <Note updateFunct={this.props.updateFunct}/>
                        </Route>
                        <Route path="/create_list" exact>
                            <CreateList updateFunct={this.props.updateFunct} details={this.state}/>
                        </Route>
                        <Route path="/recep_list" exact>
                            <RecepList updateFunct={this.props.updateFunct} details={this.state}/>
                        </Route>
                        <Route path="/verify_email" exact>
                            <VerifyEmail updateFunct={this.props.updateFunct} details={this.state}/>
                        </Route>
                        <Route path="/reset_password" exact>
                            <ResetPassword updateFunct={this.props.updateFunct} details={this.state}/>
                        </Route>
                        <Route path="/login" exact>
                            <Login updateFunct={this.props.updateFunct} details={this.state}/>
                        </Route>
                        <Route path="/profile" exact>
                            <Profile updateFunct={this.props.updateFunct} details={this.state}/>
                        </Route>
                        <Route path="/register" exact>
                            <Register updateFunct={this.props.updateFunct} details={this.state}/>
                        </Route>
                        <Route path="/Logout" exact>
                            <Logout deletAllState={this.props.updateFunct.deletAllState} rPage={rPage}/>
                        </Route>
                                        
                    </Switch>
                    
                </section>
            </React.Fragment>
        );
    }
}
  


export default Section;

