import React from "react";
import {Link, Redirect} from "react-router-dom";


class Login extends React.Component{

    constructor(props){
        super()
        this.state={
            login:props.details.login,
            prevPage:props.details.currentPageName,
            fetchURL:props.details.fetchURL,
            userLogin:"",
            password:"",
            error:"",
            otherErr:"",
            badErr:"",
            passChangeMeg:"",
            regSuccessMessage:""
        }  
        this.onchangeHandler=this.onchangeHandler.bind(this);  
        this.validation=this.validation.bind(this); 
        this.viewUnView=this.viewUnView.bind(this);   
        this.submit=this.submit.bind(this);   
    }

    submit(event){
        event.preventDefault();
        //store tages in variabled
        let userLogin=this.state.userLogin;
        let password=this.state.password;
        let ok;

        if(userLogin && password){
            document.getElementById('submit').removeAttribute("disabled");
            ok=true;
        }else{
            document.getElementById('submit').setAttribute("disabled","true");
            ok=false;
        }

        //remove backend messages
        this.setState({
            passChangeMeg:"",
            regSuccessMessage:""
        });

        //api call to login
        if(ok){
            //hide section and display loding
            document.getElementById('section').className="no_section";
            document.getElementById('section_waiting').className="section_waiting";
            document.getElementById('section_waiting_back').className="section_waiting_back";

            //start api stuff
            const system=[
                navigator.productSub,
                navigator.appVersion,
                navigator.vendor,
                navigator.hardwareConcurrency,
                navigator.deviceMemory,
                navigator.hardwareConcurrency,
                navigator.language,
                navigator.platform
            ];
            const  systemJason=JSON.stringify(system);

            const body=new FormData();
            body.append("userLogin",this.state.userLogin);
            body.append("password",this.state.password);
            body.append("system",systemJason);

            const url=this.state.fetchURL+"login.php";
            const sender={
                method:"POST",
                body:body,
                headers:{
                    Accept:"application/json",
                }
            };

            fetch(url, sender)
            .then(res=>{
                return res.json();
                // return res.text();
            })
            .then(res=>{
                // console.log(res);
                let error=res[0];
                let success=res[1];
                let otherErr=res[2];

                this.setState({error:error});
                this.setState({otherErr:otherErr});
                if(success[0] && success[1]){
                    //if login sucess
                    sessionStorage.setItem("sessIdUse4", JSON.stringify(success));
                    this.props.updateFunct.login();
                }
                document.getElementById('section').className="";
                document.getElementById('section_waiting').className="no_section";
                document.getElementById('section_waiting_back').className="no_section";    
            })
            .catch(res=>{
                let response;
                // console.log(res);
                if(typeof res==="object"){
                    if(String(res)==="TypeError: Failed to fetch"){
                        if(navigator.onLine){
                            response="Connection failed, Please check your connection";
                        }else{
                            response="You are currently offline";
                        }
                    }else{
                        response="Fatal Error, Please contact the admin";
                    }
                }else{
                    if(navigator.onLine){
                        response="Fatal Error, Please contact the admin";
                    }else{
                        response="You are currently offline";
                    }
                }
                this.setState({badErr:response});
                document.getElementById('section').className="";
                document.getElementById('section_waiting').className="no_section";
                document.getElementById('section_waiting_back').className="no_section";    
            });
        }
    }

    componentDidMount(){
        let currentPageName=window.location.pathname;
        this.props.updateFunct.updatePageName(currentPageName);

        document.getElementById('visibility1').addEventListener("click", this.viewUnView);
        document.getElementById('submit').addEventListener("click", this.submit);
        document.getElementById('userLogin').addEventListener("keyup", this.validation);
        document.getElementById('password').addEventListener("keyup", this.validation);

        //to unset all message sessions
        setTimeout(
            ()=>{
                if(sessionStorage.getItem("loginFirst")!==null){ sessionStorage.removeItem("loginFirst");}
                if(sessionStorage.getItem("reloginLink")!==null){ sessionStorage.removeItem("reloginLink");}
                if(sessionStorage.getItem("reloginMeg")!==null){ sessionStorage.removeItem("reloginMeg");}
            },1000
        );  
        if(sessionStorage.getItem("passChangeMeg")!==null){
            let passChangeMeg=JSON.parse(sessionStorage.getItem("passChangeMeg"));
            this.setState({
                passChangeMeg:passChangeMeg["passChangeMeg"],
                userLogin:passChangeMeg["userLogin"]
            });
            sessionStorage.removeItem("passChangeMeg");
        } 
        if(sessionStorage.getItem("regSuccessMessage")!==null){
            let regSuccessMessage=JSON.parse(sessionStorage.getItem("regSuccessMessage"));
            this.setState({
                regSuccessMessage:regSuccessMessage["regSuccessMessage"],
                userLogin:regSuccessMessage["userLogin"]
            });
            sessionStorage.removeItem("regSuccessMessage");
        }    
    }

    viewUnView(event){
        let visibility=event.target;
        let input=event.target.previousElementSibling;

        if(input.type==="password"){
            visibility.innerHTML="visibility"; 
            input.type="text"; 
        }else{
            visibility.innerHTML="visibility_off"; 
            input.type="password";
        } 
    }

    validation(event){
        let name=event.target.name;
        //store tages in variabled
        let userLogin=this.state.userLogin;
        let password=this.state.password;

        if(name==="userLogin"){
            if(this.state.error['userExistMessage']){delete this.state.error['userExistMessage'];}
            this.setState({error:this.state.error});
        }
        if(name==="password"){
            if(this.state.error['passwordMessage']){delete this.state.error['passwordMessage'];}
            this.setState({error:this.state.error});

        }

        if(userLogin && password){
            document.getElementById('submit').removeAttribute("disabled");
        }else{
            document.getElementById('submit').setAttribute("disabled","true");
        }
    }


    onchangeHandler(event){
        let name=event.target.name;
        let value=event.target.value;
        this.setState({[name]:value});

        //remove backend messages
        this.setState({
            error:"",
            otherErr:"",
            badErr:""
        });
    }

    static getDerivedStateFromProps(prevProps, state){
        if(state.login!==prevProps.details.login){
            let newLogin=prevProps.details.login;
            return({login:newLogin});
        }
        return({});
    }

    render(){
        const {login, prevPage, userLogin, password, error, otherErr, badErr, passChangeMeg, regSuccessMessage}=this.state;
        let loginRedirect;
        if(login && sessionStorage.getItem("loginRedirect")) {
            loginRedirect=sessionStorage.getItem("loginRedirect");
            sessionStorage.removeItem("loginRedirect");
        } 
        let pp=prevPage;
        if(prevPage==="/login" || !prevPage){pp="/";}   
        
        return(
            <React.Fragment>
                {
                    login ? 
                        loginRedirect ? <Redirect to={loginRedirect}/> : <Redirect to={pp}/> 
                    : null 
                }
                {sessionStorage.getItem("loginFirst") ? <p className='meg'>You have to login first</p> : null}
                {sessionStorage.getItem("reloginMeg") ? <p className='meg'>{sessionStorage.getItem("reloginMeg")}</p> : null}

                {passChangeMeg ? <p className='meg'>{passChangeMeg}</p> : null}
                {regSuccessMessage ? <p className='meg'>{regSuccessMessage}</p> : null}

                <form>
                    {/* {otherErr ? <p className='errorMeg'>{otherErr}</p> : null} */}
                    {otherErr.length ? <p className='errorMeg'>Fatal Error, Please contact the admin</p> : null}
                    {badErr ? <p className='errorMeg'>{badErr}</p> : null}
                    {error['emptField'] ? <p className='errorMeg'>{error['emptField']}</p> : null}
                    <div>
                        <p className="formInputLabel">Email or Username:</p> <p className='formInput'> <input type='text' name='userLogin' id='userLogin' placeholder='Email or Username' value={userLogin} onChange={this.onchangeHandler} required/></p>
                        {error['userExistMessage'] ? <p className='errorMeg'>{error['userExistMessage']}</p> : null}
                        <p className="formInputLabel">Password:</p> <p className='formInput'> <input type='password' className="password" name='password' id='password' onChange={this.onchangeHandler} value={password} placeholder='Enter your password' required/> <i className="material-icons centerVertically visibility" id="visibility1">visibility_off</i></p>
                        {error['passwordMessage'] ? <p className='errorMeg'>{error['passwordMessage']}</p> : null}

                        <input type='submit' id='submit' value='Login' />  <br/>
                        <p className='reg-or-log'> or <Link to='register'>Register</Link> </p>
                        {error['resetPass'] ? <p className='reg-or-log'><Link to='reset_password'>Forgot password?</Link></p> : null}
                    </div>
                </form>
            </React.Fragment>
        );
    }
}

export default Login;


